@media screen and (max-width: 1350px) {


    /*page-title*/
    .page-title {

        align-items: flex-start;

        &__item {




            &:first-child {
                display: flex;
                align-items: center;


            }

        }

    }

    header {
        a {
            font-size: 24px;

            &.header-nav__callback {
                margin-left: 15px;
                padding-left: 0;
            }
        }

        .header-contacts {
            a {
                font-size: 24px;
                font-weight: 600;
            }
        }
    }

    .header-nav__callback {
        .svg-zubik {
            right: -35px;
        }
    }

    .menu-contacts {
        .socials {
            &:before {
                position: absolute;
                right: -275px;
                top: -176%;
                width: 780px;
                height: 773px;
            }
        }
    }

    .header-link {
        li {
            a {
                font-size: Min(7.5vw, 52px);
            }
        }
    }

    .header-nav__menu {
        padding: 7.813vw 5.458vw;
    }

    .advantages {
        margin-top: 90px;

        .advantages__title {
            position: absolute;
            top: -175px;
            left: 72px;
            font-size: 36px;
        }
    }

    .our-territory {
        .house-animation {
            span {
                font-size: 72px;
                left: 18vw;

                &:nth-child(2) {
                    left: 53vw;
                    top: 8vw;
                }
            }
        }
    }

    .feedback {
        grid-gap: 120px;
    }

    .footer-row__animation {
        position: absolute;
        left: inherit;
        top: -169px;
        right: 0;
    }

    /*faq-page*/



    /*about*/

    .about {
        .page-title {

            &__item {
                .title_1 {
                    padding: 0 0 0 30px;

                    &::after {

                        width: 170px;
                        height: 160px;
                        top: -30px;
                        right: -130px;

                    }
                }

            }


            /*замена :after - станет видиммым на 574*/

            .image-box {
                display: none;
                width: 100%;
                max-width: 250px;
                margin: 0 auto;
            }
        }


    }

    /*about*/

    .about {

        section {
            margin-bottom: 100px;

            &:first-child {
                margin-bottom: 100px;
            }
        }

        .based {



            .alignright {
                margin: 0 0 20px 35px;
            }
        }

        .before-clinic {

            h2 {
                width: 100%;
                max-width: 900px;
            }

            .alignleft {
                margin: 0 45px 35px 0;
            }
        }

        .plans {
            .alignleft {
                margin: 0 45px 20px 0;
            }
        }

        .mission {
            .aligncenter {
                margin-bottom: 70px;
            }

            &__row {


                &__item {
                    &:first-child {
                        max-width: 590px;
                    }

                    &:last-child {
                        font-size: Min(3vw, 36px);
                        max-width: 635px;
                        line-height: 40px;
                    }
                }
            }
        }



    }

    /*doctors*/

    .doctors {
        .doctors__card-one {

            margin-top: 0px;
            margin-bottom: 60px;

            .text-box {

                max-width: 650px;


            }

        }

        .doctors__card-two {

            margin-top: 0px;
            margin-bottom: 60px;

        }

    }


    /*input type=file*/
    .template-form {
        margin-top: 200px;

        .input-box {


            .upload-btn {

                label {
                    position: relative;

                    .text-box {
                        font-size: 24px;
                        display: flex;
                        cursor: pointer;
                        top: 50px;
                        left: 0;
                        position: relative;
                        z-index: 10;
                    }

                    img {
                        position: absolute;
                        left: -53px;
                        top: 0;
                        width: 160px;
                        height: 160px;
                        z-index: 1;
                    }
                }


            }
        }
    }

}

@media screen and (max-width: 1199px) {





    .btn {
        font-size: 24px;
    }

    .first-screen {
        .svg-cat {
            left: 36px;
        }
    }

    header {

        a,
        li {
            font-size: 20px;
        }
    }

    .header-logo {
        min-width: 230px;
    }

    .header-link {
        li {
            a {
                font-size: Min(7.5vw, 36px);
            }
        }
    }

    .services {
        padding: 145px 15px 36px;
        margin: 150px auto 24px;
    }

    .our-people__title {
        font-size: 28px;
        line-height: 0.2;
        padding-left: 16%;
        width: 100%;
        margin-top: 40px;
    }

    .our-people {
        padding: 60px 0 72px;
        position: relative;
    }

    .our-people__subtitle {
        position: static;
        max-width: 710px;
        padding-left: 16%;
        font-size: 24px;
    }

    .our-people__content {
        flex-direction: column;
    }

    .our-doctors {
        .show-more {
            position: absolute;
            top: calc(100% - 35px);
            left: inherit;
            right: 0;
        }
    }

    .our-doctors__item {
        border-bottom: 1px solid #000;

        &:nth-child(even) {
            .our-doctors__item-photo {
                text-align: left;
            }
        }

        &:nth-child(odd) {
            .our-doctors__item-photo {
                text-align: right;
            }
        }

        a {
            display: flex;
            flex-direction: column;
        }

        .long-arrow {
            opacity: 1;

            span {
                width: 100px;
            }
        }
    }

    .our-doctors__item-photo img {
        position: static;
        opacity: 1;
        z-index: 0;
        overflow: initial;
        width: auto;
        transform: none;
        height: auto;
        border-radius: 0;
        transform: none;
        margin: 0;
    }

    .our-doctors__item-name {
        margin-bottom: 12px;
        font-size: 32px;
    }

    .our-doctors__item-position {
        font-size: 20px;
        margin-bottom: 35px;
    }

    .our-doctors__item-photo {
        order: 1;
    }



    /*template-form*/


    .template-form {
        margin-top: 120px;

        label,
        .hello,
        input {
            white-space: nowrap;
            font-size: 36px;
        }

        .hello {
            margin-left: 110px;
        }

        textarea {
            height: 56px;
            min-height: 56px;
            margin-top: 0;
            font-size: 24px;
            margin-bottom: 18px;
            padding-top: 12px;

            &::placeholder {
                line-height: 1.5;
                font-size: 24px;
            }
        }

        input,
        textarea {
            margin-top: 0;
            height: 48px;
        }
    }

    .form-variant {
        margin-bottom: 100px;

        .col-md-6 {
            display: flex;
            align-items: center;

            .form-variant__description {
                width: 100%;
                max-width: 760px;
                align-self: flex-end;
            }
        }

        .svg-signature {
            fill: $white;
        }

        .form-variant__signature-box {
            display: flex;
            align-items: center;

            .btn {
                font-weight: 400;
                margin-left: -100px;
            }
        }

        h3 {
            margin: 20px 0 0 -70px;
        }

    }

    .template-map {
        background-position: -175px center;
        margin-top: 120px;

        &__custom-border {
            margin-left: 30px;
            padding: 48px 0;
        }

        &__title,
        &__subtitle {
            line-height: 26px;
            font-size: 20px;
        }
    }

    .map-accordion-item {
        font-size: 18px;

        button {
            font-size: 20px;
        }
    }

    .accordion-arrow {
        margin: 7px 0 0 15px;
    }

    .accordion-collapse {
        li {
            font-size: 20px;

            &:before {
                display: none;
            }
        }
    }

    .blog-navigation {
        font-size: 18px;
        line-height: 24px;
        margin-top: 36px;
        text-align: left;
    }


    /*faq-page*/


    .faq-page {


        .faq-page__accordion {

            &:first-child {
                background-image: none;
            }

            &:nth-child(odd) {
                h4 {
                    position: relative;

                    &:after {
                        content: "";
                        background-image: url(assets/img/faq-title-after.png);
                        background-repeat: no-repeat;
                        background-position: right center;
                        background-size: contain;
                        position: absolute;
                        width: 118px;
                        height: 78px;
                    }
                }
            }

            &:nth-child(even) {
                h4 {
                    position: relative;

                    &:after {
                        content: "";
                        background-image: url(assets/img/faq-title-after-2.png);
                        background-repeat: no-repeat;
                        background-position: right center;
                        background-size: contain;
                        position: absolute;
                        width: 118px;
                        height: 78px;
                    }
                }
            }

            >div {
                width: 80%
            }


        }
    }

    /*services*/



    .services-page {}


    /*vacancies*/



    .vacancies {}



    /*single-vacancy*/
    .single_vacancy {



        .single_vacancy__title {
            display: flex;
            flex-direction: column;

            p {
                margin-bottom: 30px;
                font-size: Min(4vw, 24px);
            }

            h5 {
                text-align: right;
                position: relative;


            }
        }

    }

    /*school*/


    .school {

        .page-title {

            &__item {
                width: 100%;

                .title_1 {
                    padding: 0 0 0 0;

                    span {
                        margin: 0 0 0 24px;
                    }
                }


            }

        }
    }


    /*faq-page*/



    .faq-page {
        .page-title {

            &__item {


                .title_1 {

                    padding: 0 0 0 30px;

                    span {


                        &::before {

                            width: 180px;
                            height: 175px;

                        }
                    }
                }


            }

        }
    }


    /*contacts*/


    .contacts {

        .page-title {


            &__item {
                width: 100%;

                .title_1 {
                    padding: 0 0 0 20px;

                }




            }

        }

    }

    /*about*/

    .about {
        .page-title {

            &__item {
                .title_1 {
                    padding: 0 0 0 30px;

                    &::after {

                        width: 150px;
                        height: 120px;
                        top: -20px;
                        right: -140px;

                    }
                }

            }

        }

        .before-clinic {

            &__title {

                h2 {

                    max-width: 700px;
                }

            }

            a {

                .image-box {

                    bottom: -20%;

                }
            }

        }





        .mission {
            .aligncenter {
                margin-bottom: 50px;
            }

            &__row {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                margin: 0 auto;

                &__item {
                    &:first-child {
                        margin-bottom: 30px;
                    }

                    &:last-child {
                        font-size: 24px;
                        max-width: 760px;
                        line-height: 36px;

                        p {
                            letter-spacing: 5px;
                        }
                    }
                }
            }
        }




    }

    .documents {

        .image-box {

            a {

                .alignleft {
                    margin: 0 30px 170px 0;
                }
            }
        }

    }


    /*forms*/

    .template-form {


        .input-box {
            display: flex;

            .file-upload {
                display: flex;
                align-items: flex-end;

                label {
                    display: flex;
                    flex-direction: column;
                }

                input {
                    margin: 0 0 10px;
                }
            }
        }
    }

    .attention {
        overflow: hidden;
        padding: 84px 40px 115px;
        margin: 60px -25px;
    }


    .social-list {
        span {
            width: 100%;
            margin-bottom: 24px;
        }
    }

    .input-box{
        label[for=file] {
            height: 35px;
            padding-top: 12px;
        }
        .input-file {
            height: 39px;
            font-size: 20px;
            margin-left: 15px;
            margin-top: 9px;
        }
    } 

  
}
