@media (min-width: 1200px) {

    .our-doctors__item-photo {

        img {
            position: absolute;
            z-index: -1;
            left: 50%;
            top: -90%;
            overflow: hidden;
            width: 60px;
            height: 60px;
            border-radius: 50%;
            transform: translateX(0) rotate(0deg);
            opacity: 0;
            transition: .3s ease-in-out;
            margin-top: 90%;
        }
    }

    .our-doctors__item {
        &:nth-child(even) {
            &:hover {
                .our-doctors__item-photo img {
                    width: 19vw;
                    top: 23%;
                    height: auto;
                    transform: translateX(150%) rotate(1080deg);
                    border-radius: 0;
                    margin: 0;
                    opacity: 1;
                }
            }
        }

        &:nth-child(odd) {
            &:hover {
                .our-doctors__item-photo img {
                    width: 19vw;
                    top: 23%;
                    height: auto;
                    transform: translateX(-250%) rotate(-1080deg);
                    opacity: 1;
                    border-radius: 0;
                    margin: 0;
                }
            }
        }

        &:last-child {
            &:hover {
                .our-doctors__item-photo {
                    img {
                        top: -7vw;
                    }
                }
            }
        }
    }
}
