.our-territory {
    position: relative;
    padding: 50px 0;

    &:before,
    &:after {
        position: absolute;
        right: 0;
        top: 0;
        width: 90%;
        height: 1px;
        background-color: $black;
        content: "";
    }

    .col-md-4 {
        &:nth-child(2) {
            .img-box {
                margin-top: 180px;
                max-width: 75%;
                margin-left: auto;
            }
        }

        &:last-child {
            .img-box {
                margin-top: 90px;
                max-width: 55%;
                margin-left: auto;
            }
        }
    }

    .house-animation {
        margin-bottom: 60px;
        display: flex;
        justify-content: center;
        position: relative;

        .svg-house,
        .svg-smok {
            max-width: 12.7vw;
        }

        span {
            font-size: 72px;
            position: absolute;
            top: 0;
            left: 25vw;

            &:nth-child(2) {
                left: 47vw;
                top: 5vw;
            }
        }
    }

    &:after {
        top: inherit;
        bottom: 0;
    }

    .img-box {
        position: relative;

        .svg-cat {
            position: absolute;
            top: 85%;
            right: 0;
        }

        .svg-green {
            position: absolute;
            top: -10%;
            right: -30%;
            z-index: -1;
        }
    }
}
