.doctor {
    padding-top: 70px;

    h1 {
        font-size: 48px;
        line-height: 64px;
        letter-spacing: 0.1em;
        margin-bottom: 70px;
    }

    .col-img {
        width: 443px;
        position: relative;

        .doctor-img {
            width: 100%;
            height: 623px;
            object-fit: cover;
            display: block;
            // если надо ????
            position: sticky;
            top: 40px;
            z-index: 1;
        }

        .abs-1,
        .abs-2 {
            position: absolute;
            max-width: 100%;
            left: 50%;
            transform: translateX(-50%);
        }

        .abs-1 {
            top: 30%;
        }

        .abs-2 {
            bottom: 23%;
        }
    }

    .col-text {
        width: calc(100% - 443px);
        padding-left: 30px;
    }

    &__row {
        display: flex;
        flex-wrap: wrap;
    }

    .btn {
        text-transform: uppercase;
        margin-left: auto;
        margin-right: 0;
        display: table;
    }
}

.skill {
    margin-top: 150px;

    &__row {
        display: flex;
        flex-wrap: wrap;
        border-bottom: 1px solid #000000;
        padding: 30px 0px;

        &:last-child {
            border-bottom: 0;
        }

    }

    &__title-row {
        width: 440px;

    }

    &__text-row {
        width: calc(100% - 440px);
        padding-left: 30px;
    }

    h4 {
        margin-bottom: 0;
        line-height: 48px;
        letter-spacing: 0.14em;
        font-weight: 600;

        &+p {
            margin-top: 50px;
        }
    }

    p {
        margin-bottom: 0;
    }
}

.directions {
    margin-top: 350px;
    margin-bottom: 250px;

    h2 {
        margin-bottom: 65px;
    }

    .direction {
        padding-top: 65px;
        padding-bottom: 65px;
        min-height: 460px;
        display: block;
        background-position: center;
        background-size: cover;

        &:hover {
            background-color: var(--background);
            background-image: var(--background);
            p {
