.our-people {
    position: relative;
    padding: 140px 0;

    &__subtitle {
        position: absolute;
        left: 21%;
        top: 320px;
        max-width: 550px;
        font-size: 24px;
    }

    &__content {
        display: flex;
        align-items: center;
        overflow: hidden;
    }

    &__title {
        display: flex;
        flex-direction: column;
        padding-left: 21%;
        line-height: 96px;
        font-size: 72px;
    }

    &__animation-block {
        position: relative;
        z-index: -1;
        max-width: 665px;
        margin-left: -10.5%;
    }

    &__animation-block__slogan {
        position: absolute;
        left: 30%;
        top: -20%;
        font-size: 4vw;
    }

    .fullscreen-bg {
        position: relative;
    }

    .svg__arrow-slogan {
        position: absolute;
        left: 90px;
        top: 100px;
        width: 100%;
    }
}
