@media screen and (max-width: 767px) {
    .btn {
        font-size: 18px;
    }


    /*titles, text*/
    h1,
    h2 {
        font-size: 36px;
    }

    /*page-title simple*/

    .page-title {
        padding: 24px 20px;
        width: 100%;
        margin: 0 auto;
        flex-direction: row-reverse;
        align-items: center;

        .title_1 {
            font-size: 36px;
            padding: 0;
            margin-right: auto;

            span {
                font-family: $font-secondary;
                margin: -8px 0 0 53px;
            }
        }


        .prev-page {
            svg {
                height: 72px;
                width: 72px;
            }
        }
    }

    ul li:before {
        min-width: 18px;
        margin-left: 10px;
        margin-right: 12px;
        margin-top: 14px;
    }

    header {

        a,
        li {
            font-size: 18px;
        }

        .header-contacts {
            margin-left: initial;
            padding: 48px 0;

            span {
                font-size: 14px;
            }
        }

        .header-nav {
            padding-left: 0;
        }
    }

    .first-screen {
        .svg-cat {
            bottom: initial;
            left: 320px;
            width: 16vw;
        }
    }

    .header-link {
        margin-top: 28px;

        li {
            a {
                line-height: 1.7;

                &:after {
                    width: 16px;
                    height: 16px;
                    margin-left: 9px;
                    margin-top: 0;
                    background-color: $pink;
                }
            }
        }
    }

    .header-nav__menu-overlay {
        display: none;
    }

    .header-nav__menu {
        &.show {
            background: $light-green;
            overflow: auto;
            flex-direction: column;
        }
    }

    .header-nav__menu-close {
        right: 28px;
        top: 28px;
        width: 36px;
        height: 36px;
    }

    .menu-contacts {
        .socials {
            margin-bottom: 36px;
        }
    }

    .header-link,
    .menu-contacts {
        width: 100%;
    }


    .menu-contacts {
        margin: 35px 0 auto 0;
        padding-left: 15px;
    }

    .first-screen {
        .btn {
            font-size: 18px;
        }
    }

    /* ----- end index.html first screen, start template style ----- */

    .price-list__item {
        margin-bottom: 50px;

        .special-container {
            padding: 24px 0 12px;
        }

        &-title {
            font-size: 24px;
            padding: 24px 0 12px;
        }

        &-list {
            font-size: 14px;
        }

        .price {
            font-size: 18px;
        }
    }





    /* ----- End template style ----- */



    .block-about {
        padding: 55px 5px;
        font-size: 18px;

        span {
            padding: 0 0 12px;
        }
    }

    .block-about__top-text,
    .block-about__bottom-text {
        font-weight: 900;
    }

    .block-about__top-text {
        width: 50%;
    }

    .block-about__bottom-text {
        max-width: 220px;
    }

    .home__faq-block {
        padding-bottom: 0;

        >div {
            width: 100%;
        }

        .accordion-item {
            margin-bottom: 24px;
        }
    }

    .accordion-title {
        font-size: 14px;
        line-height: 21px;
        letter-spacing: 0.3em;
        max-width: 320px;
    }

    .accordion-body {
        font-size: 24px;
        font-size: 14px;
        line-height: 21px;
        padding: 0 0.45rem;
    }

    .accordion-item {

        .border-left {
            width: 2px;
            height: 16px;
            top: 20px;

            &:before,
            &:after {
                left: -4px;
                width: 10px;
                height: 10px;
            }
        }
    }

    .faq__video {
        display: none;
    }

    .faq .fullscreen-bg__video,
    .accordion-body__video-box {
        display: block;
        text-align: center;
    }

    .advantages {
        margin-top: 0;

        .advantages__title {
            position: relative;
            top: inherit;
            left: initial;

            div {
                position: absolute;
                top: 96px;
                left: 72px;
                font-size: 18px;
            }
        }
    }

    .advantages__row {

        &.first-child {
            margin-bottom: 93px;

            .advantages__row-item {
                width: 100%;
                max-width: 440px;
                margin: 0 auto;

                .img-box {
                    max-width: 440px;
                }

                .text-box {
                    position: absolute;
                    bottom: -53px;
                    left: 0;
                    display: grid;
                    font-size: 36px;
                }
            }
        }

        &.last-child {
            flex-direction: column;
            align-items: center;

            .advantages__row-item {
                width: 100%;
                max-width: 440px;
                margin: 0 auto 90px;

                .img-box {
                    max-width: 440px;
                }

                &:first-child .text-box,
                .text-box {
                    position: absolute;
                    bottom: inherit;
                    top: calc(100% - 18px);
                    left: 0;
                    display: grid;
                    font-size: 36px;
                }
            }
        }
    }


    .faq {
        padding-bottom: 36px;
    }

    .our-people {
        padding: 24px 0 36px;
        position: relative;
        overflow: hidden;

        .fullscreen-bg__video {
            width: 140%;
            margin-left: -24%;
        }

        .svg__arrow-slogan {
            width: 100%;
            max-width: 150px;
            position: absolute;
            top: 9px;
            overflow: initial;
            left: 135px;
        }
    }

    .our-doctors {
        .show-more {
            font-size: 14px;
        }
    }

    .our-doctors__item {
        &:last-child {
            border: none;
        }

        a {
            padding: 12px 0;
        }

        .long-arrow {
            min-width: 75px;

            span {
                width: 60px;
                max-width: 60px;
            }
        }
    }

    .our-doctors__item-name {
        margin-bottom: 12px;
        font-size: 18px;
    }

    .our-people__subtitle {
        padding-left: 0;
        font-size: 14px;
    }

    .our-people__title {
        font-size: 18px;
        line-height: 1.2;
        padding-left: 0;
        width: 100%;
        margin-top: 28px;
    }

    .our-doctors__list {
        width: 100%;
    }

    .our-doctors__item-photo {
        img {
            width: 60%;
        }
    }

    .our-doctors__item-position {
        font-size: 14px;
    }

    .our-territory {
        .house-animation {
            span {
                font-size: 18px;
                top: 7vw;
                left: 10vw;
            }
        }

        .img-box {
            .svg-cat {
                max-width: 87px;
            }

            .svg-green {
                top: 76%;
                right: 71px;
                width: 212px;
            }
        }

        .col-md-4 {
            &:last-child {
                .img-box {
                    margin-left: 0;
                }
            }

            &:nth-child(2) {
                .img-box {
                    margin-top: 72px;
                }
            }
        }
    }

    .adults-territory__title {
        display: flex;
        font-size: 18px;
        flex-direction: column;
        margin-left: 40%;

        svg {
            order: -1;
            max-width: 33%;
            left: 0;
            top: 30px;
            position: absolute;
        }
    }

    .adults-territory__item {
        margin: auto;

        img {
            max-height: 41px;
        }
    }

    .zigzag {
        fill: #ffffff;
        height: 50px;
    }

    .adults-territory__title {
        margin-bottom: 48px;
    }


    .signature {
        width: 50%;
    }

    .form-variant {
        &__signature-box {
            position: relative;

            .btn {
                position: absolute;
                top: 35%;
                right: 15px;
            }
        }

        &__description {
            font-size: 14px;
            line-height: 21px;
            margin: 0 0 24px !important;
        }

        h4 {
            font-size: 24px;
            margin: 20px 0 0 0;
        }
    }


    .template-form {
        margin-top: 0;

        .input-box {
            flex-direction: column;
            margin-bottom: 12px;
        }

        .form-variant {
            margin: 12px auto 24px;
        }

        label,
        .hello,
        input {
            font-size: 18px;
            line-height: 21px;
            margin-top: 0;
            padding: 12px 0 9px;

            &::placeholder {
                text-align: left;
                font-size: 14px;
                line-height: 21px;
            }
        }

        .hello {
            margin-left: 0;
        }

        textarea {
            height: 56px;
            min-height: 56px;
            margin-top: 0;
            font-size: 18px;
            margin-bottom: 18px;
            padding-top: 12px;
            padding: 24px 0 0 0;

            &::placeholder {
                line-height: 2;
                text-align: left;
                font-size: 14px;
            }
        }

        .btn-box {
            margin: 24px 0;

            .btn {
                margin: 0 auto;
            }

        }
    }

    .template-map {
        margin: 62px -20px 0;
        width: calc(100% + 40px);
        min-height: 300px;
        overflow: hidden;
        background: url(assets/img/2.jpg) no-repeat #71c1c2;
        background-size: cover;
        display: flex;
        background-position: 30%;
        align-items: flex-end;
        padding: 0;
        justify-content: flex-start;

        &__title {
            max-width: 168px;
        }

        &__title,
        &__subtitle,
        .accordion-item {
            font-size: 14px;
            line-height: 36px;
        }

        &__special-box {
            width: 100%;
            min-height: 160px;
            padding: 5px 17px 5px;
        }

        .accordion-collapse {
            display: block !important;
        }
    }

    .map-accordion-item {
        padding-left: 20px;

        button {
            font-size: 14px;
            position: relative;

            &:hover {
                .map-accordion__accordion-arrow {
                    filter: none;
                }
            }
        }

    }

    .accordion-collapse {
        li {
            font-size: 14px;
        }

    }


    .map-accordion__accordion-arrow {
        width: 10px;
        height: 10px;
        margin: 0;
        position: absolute;
        top: 5px;
        left: -20px;
        background-image: none;
        background-color: #413D58;
        border-radius: 50%;
    }

    .collapsed {
        .map-accordion__accordion-arrow {
            width: 10px;
            height: 10px;
            margin: 0;
            position: absolute;
            top: 5px;
            left: -20px;
            background-image: none;
            background-color: #413D58;
            border-radius: 50%;

        }
    }

    .template-map__custom-border {
        min-width: 256px;
        min-height: 240px;
        max-width: initial;
        width: auto;
        margin: 0;
        padding: 15px 0;
        display: flex;
        align-items: flex-end;
        border-radius: 0% 89% 9% 0% / 32% 93% 44% 0%;
        justify-content: flex-start;
    }



    .template-redirect {
        padding: 15px calc(19.3% - 15px) 22px 15px;
        margin: 15px 0 60px 0;
        position: relative;
        border-bottom: 1px solid $black;

        .template-redirect__title {
            font-size: 18px;
            margin-bottom: 27px;
        }

        .template-redirect__content {
            font-size: 14px;
            line-height: 21px;
        }

        .template-redirect__redirect {
            right: 0;
        }
    }

    .article-list__item {
        padding: 0;
        margin-bottom: 48px;

        &__title {
            width: 100%;
            margin: 18px auto 24px;
            font-size: 22px;
        }

        &__category {
            font-size: 16px;
        }
    }

    .single-srvices {
        .page-title {
            h1 {
                display: flex;
                flex-direction: column;
                width: calc(100% - 85px);

                .font-weight-light {
                    margin-left: 0;
                }
            }
        }

        &__descriptions {
            flex-direction: column;
            font-size: 14px;

            .text-box {
                margin-bottom: 30px;
                max-width: 100%;
                padding: 0;
            }
        }

        &__reminder {
            font-size: 18px;
            line-height: 21px;
            margin: 30px auto 60px;
        }

        &__quote {
            padding-left: 0;
            font-size: 14px;
        }

        &__quote__text-box {
            &:before {
                left: inherit;
                right: 0;
            }
        }

        &__decor-box {
            border-bottom: none;

            .img-box-sm {
                display: none;
            }
        }
    }

    footer {
        margin-top: 60px;
    }

    .footer-row__animation {
        top: 115px;
        width: 100px;
    }


    .wp-pagenavi {
        a {
            color: #000;
            font-size: 18px;
            margin: 0px 6px;
        }

        .current {
            font-size: 26px;
            margin: 0 5px;
        }

        .extend:before {
            width: 30px;
        }

        .first,
        .previouspostslink,
        .last,
        .nextpostslink {
            margin: 0 6px;
            font-size: 30px;
            font-family: monospace;
            position: relative;
            top: 0px;
        }
    }

    /*faq-page*/



    .faq-page {
        .accordion {
            &-title {
                max-width: 100%;
            }

            &-item {

                margin-bottom: 35px;
            }

        }

        .faq-page__accordion {
            display: flex;
            flex-direction: column;
            margin-bottom: 50px;

            h4 {
                font-size: 14px;
                padding-left: 0;
                margin-bottom: 30px;
            }

            &:nth-child(odd) {
                h4 {
                    position: relative;

                    &:after {

                        width: 62px;
                        height: 36px;
                    }
                }
            }

            &:nth-child(even) {
                h4 {
                    position: relative;

                    &:after {

                        width: 62px;
                        height: 36px;
                    }
                }
            }

            >div {
                width: 100%
            }


        }
    }

    /*single-doctor*/


    .single-doctor {
        &__video-block {
            margin-top: 50px;

            .video-block {


                a {



                    &::before {
                        display: none;
                    }
                }



            }


        }

    }


    /*faq-page*/



    .faq-page {
        .page-title {

            &__item {
                width: 100%;

                .title_1 {
                    padding: 0 0 0 0;
                    font-size: 36px;


                    span {
                        margin: 0 0 0 60px;
                        position: relative;

                        &::before {

                            width: 70px;
                            height: 68px;
                            left: 150px;
                            top: -45px;
                        }
                    }
                }


            }

        }
    }


    /*about*/

    .about {
        .page-title {

            &__item {
                .title_1 {
                    font-size: 36px;

                    &::after {

                        width: 100px;
                        height: 90px;
                        top: -20px;
                        right: -100px;

                    }
                }

            }

        }

        .before-clinic {

            &__title {



                .image-box {

                    top: -165px;

                }

            }



        }

        .plans {

            .image-box {
                position: absolute;
                top: 115px;
                right: -170px;
                z-index: -1;
                width: 100%;
                max-width: 415px;
            }
        }



    }

    /*doctors*/

    .doctors {

        .doctors__card-one {
            flex-direction: column-reverse;
            position: relative;

            .text-box {

                margin-bottom: 0;
                max-width: 100%;

                &::after {
                    display: none;
                }
            }

            .image-box {
                margin-right: 0;
                max-width: Min(60vw, 500px);

            }

            &:after {
                content: "";
                position: absolute;
                top: calc(100% + 30px);
                left: 0;
                width: 100%;
                border-bottom: 1px solid $black;
            }

        }

        .doctors__card-two {

            flex-direction: column;
            align-items: flex-start;
            position: relative;

            .text-box {

                margin-bottom: 0;
                max-width: 100%;
                text-align: start;

                &::after {
                    display: none;
                }
            }

            .image-box {
                margin-left: 0;
                max-width: Min(60vw, 500px);

            }

            &:after {
                content: "";
                position: absolute;
                top: calc(100% + 30px);
                left: 0;
                width: 100%;
                border-bottom: 1px solid $black;
            }

        }

    }

    /*forms*/

    .template-form {


        .input-box {
            display: flex;

            .file-upload {
                display: flex;
                align-items: flex-start;
                flex-direction: column;
            }

            .upload-btn {

                label {
                    position: relative;

                    .text-box {
                        font-size: 18px;
                        display: flex;
                        cursor: pointer;
                        top: 65px;
                        left: 0;
                        position: relative;

                    }

                    img {
                        position: absolute;
                        left: -48px;
                        top: 41px;
                        width: 130px;
                        height: 130px;

                    }
                }
            }
        }
    }

    .attention {
        flex-direction: column;
        margin: 24px -40px 36px;
        padding: 24px 40px 36px;

        &__img-box {
            width: 221px;
            min-width: 221px;
            margin: 0 auto;
        }

        &__title {
            font-size: 32px;
            margin-top: 12px;
        }
    }

    .post-nav {
        padding: 36px 0 48px;

        a {
            font-size: 22px;
            line-height: 40px;
            padding-bottom: 12px;
        }

        &__next,
        &__prev {
            text-align: center;
        }
    }

    .social-list {
        margin-top: 24px;
        padding: 36px 0 24px;

        a {
            margin: 0 45px 30px 0;
        }
    }

    .list-services__title {
        font-size: 32px;
        margin: 24px -15px 36px;
    }

    .author {
        margin-bottom: 90px;
    }

    .section-accordion-title {
        font-size: 24px;
        margin-bottom: 24px;
    }

    .faq-page {
        .home__faq-block {
            >div {
                width: 100%;
                max-width: 396px;
            }
        }
    }

    .modal {
        &.map {
            .modal-header {
                padding: 24px 1rem 12px;

                .title_4 {
                    font-size: 20px;
                }
            }

            .modal-body {
                padding: 0 1rem 1rem;
            }
        }
    }

    .input-box {
        .input-file {
            height: 28px;
            font-size: 14px;
            margin-left: 0;
            margin-top: 9px;
            background-position: 3px -2px;
            padding: 0 36px;
        }

        label[for=file] {
            margin-bottom: 12px;
        }
    }
}
