.single-doctor__video-block {
    margin-top: 200px;

    .video-block {
        display: flex;
        justify-content: center;
        position: relative;

        a {
            position: relative;

            .play-button {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                background-color: #fff;
                background-position: center;
                background-size: contain;
                border-radius: 50%;
                width: 178px;
                height: 178px;
                transition: .2s;

                img {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-38%, -50%);
                }
            }

            &:hover {
                .play-button {
                    opacity: .8;
                }
            }
			&::before {
				content: "";
				position: absolute;
				background-image: url(assets/img/video-block-vector.png);
				background-repeat: no-repeat;
				background-size: contain;
				top: -200px;
				left: -160px;
				z-index: -1;
				width: 519px;
				height: 510px;
			}
        }



    }


}
