.markup {
    .btn {
        margin-bottom: 12px
    }
}

.markup-colors {
    div {
        height: 63px;
        width: 100%;
        margin-top: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: $black;
        font-size: 18px;
        font-weight: 900;

        span {

            &:first-child {
                background-color: $black;
                color: #63d4ff;
                padding: 2px 5px 2px 10px;
            }

            &:last-child {
                background-color: $black;
                color: #e8c058;
                padding: 2px 10px 2px 5px;
            }
        }
    }

    .violet {
        background-color: $violet
    }

    .turquoise {
        background-color: #74C6C7;
    }

    .light-green {
        background-color: #BACF39;
    }

    .pink {
        background-color: #EF7D9A;
    }

    .deep-violet {
        background-color: #413D58;
    }

    .grey {
        background-color: #A5A5A5;
    }

    .white {
        background-color: $white;
        border: 1px solid $black
    }

    .black {
        color: $white;
        background-color: $black;
        border: 1px solid $white
    }
}

.font-weight-light {
    font-family: $font-secondary;
}

.font-weight-normal {
    font-weight: 400
}

.font-weight-bold {
    font-weight: 900
}

h1,
.title_1,
h2,
.title_2,
h3,
.title_3,
h4,
.title_4,
h5,
.title_5,
h6,
.title_6 {
    font-family: $font-primary;
    color: $black;
    margin-bottom: .2em;
    line-height: 1.3333;
}

h1,
.title_1 {
    font-size: 96px;
    display: flex;
    flex-direction: column;
    text-transform: uppercase;

    span {
        font-family: $font-secondary;
        margin: 0 0 0 120px;
    }
}

h2,
.title_2 {
    font-size: MIn(4vw, 72px);
    text-transform: uppercase;
    margin-bottom: 30px;
}

h3,
.title_3 {
    font-size: MIn(4vw, 48px);
    text-transform: uppercase;
}

h4,
.title_4 {
    font-size: 36px;
}

h5,
.title_5 {
    font-size: 24px;
}

h6,
.title_6 {
    font-size: 16px;
}

p {
    @extend %lastChild;
}

a {
    color: $violet;
    text-decoration: none;

    &:hover {
        color: $light-green;
    }
}


.btn {
    padding: var(--btn-py) var(--btn-px);
    font-size: var(--btn-font-size);
    font-weight: var(--btn-font-weight);
    line-height: var(--btn-line-height);
    border-radius: var(--btn-border-radius);
}

.btn {
    text-align: center;
    outline: none;
    position: relative;
    font-size: 24px;
    line-height: 1;
    text-transform: uppercase;
    font-family: $font-primary;

    svg {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        transform: translate(-50%, -50%);
    }

    &:hover {
        .path-btn {
            animation: btn_stroke .5s ease-in-out forwards;
            animation-direction: normal;
        }
    }

    &:active:focus {
        box-shadow: none;
        outline: none
    }
}


.btn-primary {
    color: $black;
    background-color: transparent;
    border: none;

    &:hover {
        color: $black;
        background-color: transparent;
        border: none;
    }

    &:focus {
        color: $black;
        background-color: transparent;
        border: none;
    }
}

.btn-secondary {
    color: $black;
    background-color: transparent;
    border: none;

    &:hover {
        color: $black;
        background-color: transparent;
        border: none;
    }

    &:focus {
        color: $black;
        background-color: transparent;
        border: none;
        box-shadow: none;
    }

    .path-btn {
        stroke: $turquoise;
    }
}

.btn-success {
    color: $white;
    background-color: $violet;

    &:hover,
    &:focus {
        color: $white;
        background-color: $light-green;
    }
}

.btn-link {
    color: $grey;
    font-weight: 700;
    padding: 0;
    border-radius: 0;
    font-size: 12px;

    &:hover {
        color: $pink;
        text-decoration: none
    }
}

ol {
    padding: 0;
    counter-reset: li;

    li {
        list-style: none;
        list-style-position: outside;

        &:before {
            content: counter(li, decimal) ".";
            counter-increment: li;
            margin-left: 20px;
            margin-right: 20px;
            font-size: 24px;
            font-weight: 900;
            line-height: 30px;
            color: #8d7db9;
        }
    }
}

ol ol {
    padding-left: 30px
}

ul {
    padding: 0;
    list-style: none;

    li {
        &:before {
            content: "";
            margin-left: 20px;
            margin-top: 18px;
            margin-right: 20px;
            background-color: $black;
            min-width: 33px;
            height: 1px;
            display: inline-flex;
        }
    }
}

ul,
ol {
    li {
        display: flex;
        align-items: flex-start;
        list-style: none;

        &[class] {
            &:before {
                display: none
            }
        }
    }
}

input[type="checkbox"],
input[type="radio"] {
    display: none;

    &+label {
        color: #595959;
        cursor: pointer;

        &:hover {
            color: $black;
        }

        &:before {
            content: '';
            padding: 9px;
            display: inline-block;
            vertical-align: middle;
            cursor: pointer;
            margin-right: 19px;
            width: 24px;
            height: 24px;
            background-color: $turquoise;
            border: 1px solid $turquoise;
            border-radius: 4px
        }
    }
}

input[type=radio]+label:before {
    border-radius: 50%
}

input[type=radio]:checked+label:before {
    border-radius: 50%;
    background: url(assets/img/radio.png) center no-repeat;
    background-size: contain;
}

input:checked+label {
    color: $black;

    &:before {
        content: '';
        background: url(assets/img/checkbox.png) center no-repeat;
        background-size: contain;
    }
}

.scroll {
    max-height: 360px;
    @include scroll
}

.site-map {
    ol {
        padding-left: 40px;
        padding-bottom: 20px;

        li {
            position: relative;
            border-left: 2px solid $pink;
            display: block;

            &:hover {
                cursor: pointer;

                &>a {
                    text-decoration: underline
                }

                &:before {
                    background-color: $light-green;
                }
            }

            a {
                line-height: 40px;
                margin-left: 15px;
                height: 60px;
                display: flex;
                align-items: end;
            }

            &:before {
                content: "";
                position: absolute;
                left: -26px;
                top: 36px;
                width: 10px;
                height: 10px;
                background-color: $pink;
                border-radius: 50%
            }

            &:after {
                content: "";
                position: absolute;
                left: -2px;
                top: 47px;
                width: 50%;
                height: 15px;
                border-left: 2px solid $pink;
                border-bottom: 2px solid $pink
            }
        }
    }
}

.tooltip-inner {
    background-color: #000;
    min-width: 710px !important;
    padding: 5px;
}

.template-items {
    padding: 30px;
    border: 1px solid #e2e2e2;
}


.fullscreen-bg__video {
    width: 100%;
}


.path-btn {
    stroke-dashoffset: 3422.425537109375;
    stroke-dasharray: 3422.425537109375;
    // var path = document.querySelector('path');
    // console.log(path.getTotalLength()); для вычисления stroke-dasharray
    stroke: $pink;
    stroke-width: 5;
    animation-direction: reverse;
    fill: none;
}

@keyframes btn_stroke {
    0% {
        stroke-dashoffset: 3422.425537109375;
    }

    100% {
        stroke-dashoffset: 0;
    }
}

.svg-signature {
    width: 100%;
}

.path-signature {
    stroke-dashoffset: 3543.8037109375;
    stroke-dasharray: 3543.8037109375;
    // var path = document.querySelector('path');
    // console.log(path.getTotalLength()); для вычисления stroke-dasharray
    stroke: #979797;
    stroke-width: 2;
    animation-direction: reverse;
    animation: signature_stroke 4s ease-in-out infinite;

}


@keyframes signature_stroke {
    0% {
        stroke-dashoffset: 3543.8037109375;
    }

    50% {
        stroke-dashoffset: 0;
    }

    100% {
        stroke-dashoffset: 3543.8037109375;
    }
}

.svg-cat {
    animation: cat 1.2s infinite;
    width: 100%;
    max-width: 165px;
    fill: #fff;
}

@keyframes cat {
    0% {
        transform: rotate(-4deg);
    }

    75% {
        transform: rotate(4deg);
    }

    100% {
        transform: rotate(-4deg);
    }
}

