.svg-zubik {
    animation: zubik 1.2s infinite;
    width: 100%;
}

@keyframes zubik {
    0% {
        transform: rotate(0deg);
    }

    8% {
        transform: rotate(20deg);
    }

    16% {
        transform: rotate(-20deg);
    }

    24% {
        transform: rotate(20deg);
    }

    38% {
        transform: rotate(-20deg);
    }

    48% {
        transform: rotate(20deg);
    }

    56% {
        transform: rotate(-20deg);
    }

    64% {
        transform: rotate(0deg);
    }

    72% {
        transform: rotate(-20deg);
    }

    80% {
        transform: rotate(0deg);
    }

    88% {
        transform: rotate(0deg);
