.single-srvices {
	.page-title {

		&__item {
			width: 100%;


		}

	}
    .container {
        max-width: 1569px;
        padding: 0 12px;
    }



    &__descriptions {
        display: flex;
        align-items: center;

        .text-box {
            padding: 72px 35px 0 0;
            max-width: 442px;
        }
    }

    &__reminder {
        @extend .title_3;
        margin: 200px auto 72px;
    }

    &__quote {
        display: flex;
        width: 100%;
        padding-left: 442px;
        margin: 53px 0 42px;
    }
    &__quote__text-box {
        position: relative;
        &:before{
            content: "";
            position: absolute;
            top: -40px;
            left: -72px;
            width: 136px;
            height: 169px;
            background: url(assets/img/icon-quote.png) center no-repeat;
            background-size: contain;
            z-index: -1;
        }
    }

    &__decor-box{
        display: flex;
        border-bottom: 1px solid #000;
        .img-box-sm {
            width: 442px;
        }
    }

    &__information-box__item{
        .img-box{
            height: 200px;
            display: flex;
            align-items: center;
        }

        .text-box{
            @extend .title_5;
            max-width: 442px;
        }
    }
}
