footer {
    @include container;
    width: 100%;
    margin-top: 228px;
}

.footer-row {
    display: flex;
    position: relative;
    padding-bottom: 25px;
    border-bottom: 1px solid $black;
    grid-gap: 30px;
    &__logo {
        width: 100%;
        max-width: 757px;
        display: flex;
    }

    &__animation {
        margin-right: auto;
        max-height: 165px;
        position: absolute;
        left: 470px;
        top: -80px;
    }

    &__social {
        display: flex;
        align-items: flex-end;
        width: 100%;
        max-width: 757px;

        a {
            color: $black;
            font-weight: 900;
            &:hover {
                color: $turquoise;
            }
        }

        ul {
            display: flex;
            max-width: 757px;
            width: 100%;
            justify-content: space-between;
            margin: 0 0 0 auto;

            li {
                &:before {
                    display: none;
                }
            }
        }
    }
}

.footer-copy {
    display: flex;
    justify-content: space-between;
    padding-top: 75px;
    grid-gap: 30px;
    nav {
        width: 100%;
        margin: 0 0 0 auto;
        max-width: 757px;
    }

    ul {
        a {
            color: $black;

            &:hover {
                color: $turquoise;
            }
        }

        li {
            width: 100%;
            display: flex;
            justify-content: space-between;
            margin-bottom: 12px;

            &:before {
                display: none;
            }
        }
    }
    p{
        width: 100%;
    max-width: 757px;
    display: flex;
    }
}
