.contacts {

    .page-title {


        &__item {
            width: 100%;

            .title_1 {
                font-size: Min(5vw, 91px);
                padding: 0 0 0 50px;
                position: relative;

                &::before {
                    content: "";
                    background-image: url(assets/img/contacts-title-vector.png);
                    background-size: cover;
                    width: 340px;
                    height: 325px;
                    position: absolute;
                    right: -340px;
                    top: -40px;
                    z-index: -1;
                }


            }




        }

    }

}
