.services {
    position: relative;
    margin: 150px -20px 140px;
    padding: 140px 20px 105px;
    background-size: cover;
    background-repeat: no-repeat;
    &.active{
         &:before, &:after {
            background-color: transparent;
         }
    }
    &:before,
    &:after {
        position: absolute;
        right: 0;
        top: 0;
        width: 90%;
        height: 1px;
        background-color: $black;
        content: "";
    }

    &:after {
        top: inherit;
        bottom: 0;
    }

    a.d-sm-none {
        display: flex;
        justify-content: flex-end;
        color: $black;
        font-size: 14px;
        padding-right: 20px;

        &:hover {
            color: $turquoise;
        }
    }
}

.service__item {
    &:hover {
        .services__item__title {
            color: $black;
            &:before{
                opacity: 1;
            }
        }

        .services__item__description {
            opacity: 1;
            color: #000;
        }
    }
}

.services__link {
    position: absolute;
    left: 70px;
    top: 165px;
    padding-left: 100px;
    color: $black;

    .svg-nice {
        position: absolute;
        left: -120px;
        top: -75px;
    }

    .title_4 {
        margin-bottom: 0;
    }
    &:hover{
        color: $black;
       p{
        text-decoration: underline;
       }
    }
}

.service__list {
    display: flex;
    flex-direction: column;
    width: 60%;
    margin-left: auto;
}

.services__item__title {
    position: relative;
    color: $black;
    line-height: 1;
    font-size: 48px;
    padding-right: 75px;

    &:before {
        content: "";
        position: absolute;
        top: 25px;
        right: 0;
        height: 20px;
        width: 65px;
        opacity: 0;
        background: url(assets/img/long-arrow.svg) center no-repeat;
    }
}

.services__item__description {
    padding-top: 12px;
    padding-bottom: 24px;
    border-bottom: 1px solid $black;
    opacity: 0;
    transition: .3s;
}

.circle {
    margin-left: -15px;
    color: $deep-violet;
    line-height: 0;
    font-size: 150px;
}
