@import "sass/variables";
@import "sass/page/markup";
@import "sass/header";
/*=== BEGIN BASIC styles ===*/
body {
    font-family: $font-primary;
    color: $black;
    background-color: $white;
    overflow-x: hidden;
    justify-content: space-between;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    font-size: 24px;
    line-height: 1.5;
}
/*=== END BASIC styles ===*/


.markup-link {
    position: fixed;
    bottom: 15px;
    left: 15px;
    z-index: 2;
    background-color: $light-green;
    padding: 5px;
    border-radius: 50%;
    transition: .2s;
    text-decoration: none;
    font-weight: 900;
    color: $white;
    &:hover {
        background-color: $violet;
        color: $white;
    }
}

