.our-doctors {
    position: relative;
    padding: 50px 0 114px;

    .show-more {
        position: absolute;
        top: 0;
        left: 7vw;
        color: #000;

        &:hover {
            color: $deep-violet
        }
    }

    &__list {
        width: 635px;
        margin: 0 auto
    }

    &__item {
        width: 100%;
        border-bottom: 1px solid transparent;

        .long-arrow {
            opacity: 0;
        }

        &:hover {
            border-bottom: 1px solid $black;

            .long-arrow {
                opacity: 1;

                span {
                    width: 100px
                }
            }
        }

        a {
            position: relative;
            width: 100%;
            padding: 40px 15px;
            color: $black;
            transition: .3s;

            &:hover {
                color: $turquoise
            }
        }
    }

    &__item-name {
        margin-bottom: 32px;
        font-size: 48px
    }

    &__item-position {
        display: flex;
        justify-content: space-between;
        width: 100%;
        font-size: 24px;

        >span {
            display: flex;
            width: 100%
        }

    }
}
