.form-variant {
    margin-bottom: 100px;

    .col-md-12 {
        display: flex;
        align-items: center;

    }

    .svg-signature {
        fill: $white;
    }

    .form-variant__signature-box {
        display: flex;
        align-items: center;

        .btn {
            font-weight: 400;
            margin-left: -100px;
        }
    }

    h4 {
        text-transform: uppercase;
        margin: 20px 0 0 -70px;
    }

}

.template-form {
    margin-top: 200px;

    .input-box {
        display: flex;
        position: relative;

        .file-upload {
            display: flex;
            align-items: flex-end;
            width: 100%;

            label {
                display: flex;
                flex-direction: column;

            }

            input {
                margin: 0 0 30px;
                background-color: transparent;
            }
        }

        .upload-btn {
            position: absolute;
            right: 0;

            label {
				position: relative;
                .text-box {
                    font-size: 36px;
                    display: flex;
                    cursor: pointer;
                    top: 50px;
                    left: 0;
					position: relative;
					z-index: 10;
                }
				img{
					position: absolute;
					left: -55px;
					top: 0;
					width: 200px;
					height: 200px;
					z-index: 1;
				}
            }

            .hidden {
                display: inline-block;
                overflow: hidden;
                z-index: -1;
                width: 0.1px;
                height: 0.1px;
                opacity: 0;
                padding: 0;
            }
        }
    }


    label,
    .hello {
        white-space: nowrap;
        font-size: 72px;
        text-transform: uppercase;
    }

    .hello {
        margin-left: 220px;
    }

    input,
    textarea {
        width: 100%;
        padding: 0 60px;
        border: none;
        height: 48px;
        border-bottom: 1px solid #000;
        font-size: 24px;
        margin-top: 28px;

        &:focus {
            border-bottom: 1px solid #000;
        }

        &::placeholder {
            font-size: 24px;
            line-height: 72px;
            color: #a5a5a5;
            text-align: center
        }
    }

    .btn-box {
        display: flex;
        margin-right: 14%;
        margin-top: 60px;
        justify-content: flex-end;
    }

    textarea {
        overflow: hidden;
        display: flex;
        width: 100%;
        height: 48px;
        min-height: 48px;
        margin-top: 27px;
        padding: 0 60px 0;
        border: none;
        border-bottom: 1px solid $black;
        outline: none;


        &::placeholder {
            color: #a5a5a5;
            line-height: 48px;
            text-align: center
        }
    }
}
