@media screen and (max-width: 991px) {
    .btn-secondary {
        .path-btn {
            stroke-dashoffset: 0;
        }

    }

    /*page-title*/

    .page-title {
        padding: 24px 20px;
        flex-wrap: wrap;

        &__item {
            width: 100%;

            .title_1 {
                padding: 0;
                margin-right: auto;

                span {
                    margin: 0 0 0 60px;
                }
            }

            p {
                padding: 0;
                width: 100%;
            }

            &:first-child {
                display: flex;
                align-items: center;
                flex-direction: row-reverse;
                padding-bottom: 40px;

                .prev-page {
                    svg {
                        height: 72px;
                        width: 72px;
                    }
                }
            }

        }

    }


    header {
        padding-bottom: 0;
        padding-top: 18px;
        z-index: 1;

        .header-contacts {
            padding: 48px 15px 36px;
            margin-left: initial;
            order: 1;
            max-width: calc(550px - 75px);
            text-align: left;
            margin-right: auto;

            span {
                white-space: pre-wrap;
                max-width: 229px;
            }
        }
    }

    .first-screen {
        .svg-cat {
            bottom: initial;
            top: -190px;
            left: calc(100% - 16vw);
            width: 15vw;
            max-width: initial;
        }
    }

    .header-nav {
        margin-top: 40px;
        max-width: 100%;
    }

    .home {
        header {
            .header-contacts {
                display: block;
            }
        }
    }

    header {
        a.header-nav__callback {
            margin-left: 0;
        }

        a.header-nav__burger {
            padding-right: 0;
        }

        .header-contacts {
            display: none;

            a {
                margin-top: 36px;
            }
        }
    }

    a.header-nav__callback {
        order: -1;
        font-weight: 600;

        .svg-zubik {
            right: 8px;
            top: -32px;
            height: 38px;
        }
    }

    .header-nav__menu-close {
        width: 42px;
        height: 42px;
        right: 42px;
        top: 36px;
    }

    .service__list+.circle {
        display: none;
    }

    .menu-contacts {
        .socials::before {
            display: none;
        }
    }

    .header-link {

        li {
            a {
                font-size: Min(7.5vw, 36px);
            }
        }
    }

    .header-nav__menu.show:before {
        width: 300vw;
        height: 300vw;
        transform: translate(24%, -24%);
    }

    header.container-fluid {
        flex-direction: column;
    }


    /* ----- end index.html first screen, start template style ----- */



    .page-subtitle {
        padding: 0 0 30px;
        margin-top: 30px;
        font-size: 14px;
        border-bottom: 1px solid;
    }

    .bottom-animation {
        display: none;
    }

    .price {
        .page-title {
            .svg-nice {
                position: static;
                width: 50px;
            }
        }
    }

    .list-services {
        margin: 24px auto 50px;
        padding: 0 20px;

        &__decor-box {
            &:first-child {
                .list-services__item {
                    padding: 110px 0 60px;
                }
            }

            &:nth-child(2) {
                .list-services__item {
                    padding: 94px 0 60px;
                }
            }
        }

        &__decor-box {
            background-image: none !important;
            background-color: transparent !important;
            padding: 20px 0px;
            border-bottom: 1px solid #000;
        }

        &__item {
            padding: 10px 0 60px;
            min-height: 334px;
            max-width: 992px;
            margin: 0 -20px;
            background-repeat: no-repeat;

            &__title {
                font-size: 18px;
                line-height: 21px;
                margin-bottom: 17px;
                padding: 0 20px;

                .circle {
                    margin-left: -7px;
                    color: #413D58;
                    line-height: 0;
                    font-size: 57px;
                }

                .long-arrow {
                    min-width: 77px;
                    opacity: 1;

                    span {
                        width: 60px;
                    }
                }

                &:hover {
                    .long-arrow span {
                        width: 60px;
                    }
                }
            }

            &__subtitle {
                font-size: 14px;
                border: none;
                padding: 0 20px;
            }
        }
    }

    .blog-navigation {
        display: flex;
        flex-direction: column;
        margin-top: 0px;

        a {
            margin-bottom: 12px;
        }
    }

    .navigation-animation {
        display: flex;
        border-bottom: 1px solid $black;
        padding-bottom: 12px;
        margin-top: -67px;


        svg {
            margin-left: auto;
            width: 75px;
            height: 75px;
        }
    }


    /* ----- End template style ----- */

    .accordion-item {
        padding-left: 40px;

        .border-left {
            left: 10px;
        }
    }

    .advantages {
        svg.svg-heart {
            max-width: 144px;
        }
    }

    a.advantages__row-item {
        .img-box {
            max-width: 300px;
        }
    }

    .service__list {
        width: 100%;
    }

    .services {
        padding: 310px 15px 60px;
    }

    .services__item__title {
        font-size: 36px;
        margin-bottom: 84px;
    }

    .our-territory {
        .house-animation {
            span {
                font-size: 48px;
                left: 18vw;
            }
        }
    }

    .adults-territory__item {
        margin: auto;
    }


    .template-form {

        label,
        .hello,
        input {
            white-space: nowrap;
            font-size: 24px;
            padding: 0 20px;

            &::placeholder {

                font-size: 20px;
            }
        }

        textarea {
            height: 37px;
            min-height: 37px;
            font-size: 18px;
            margin-bottom: 18px;
            padding: 6px 20px 0;

            &::placeholder {
                line-height: 1;
                font-size: 20px;
            }
        }
    }

    .form-variant {
        margin-bottom: 100px;

        .col-md-12 {
            display: flex;
            align-items: center;

        }

        .svg-signature {
            fill: $white;
        }

        .form-variant__signature-box {
            display: flex;
            align-items: center;

            .btn {
                font-weight: 400;
                margin-left: -100px;
            }
        }

        h4 {
            margin: 20px 0 0 0;
        }

    }

    .template-map {
        background-position: -340px center;

        &__custom-border {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            min-height: 330px;
            max-width: 500px;
        }
    }

    .map-accordion-item {
        button {
            font-size: 20px;
        }
    }

    .feedback {
        flex-direction: column;
        margin-top: 60px;
        grid-gap: 56px;

        &__email__title {
            display: inline-flex;
            font-size: 18px;
            flex-direction: column;
            line-height: 21px;
            font-weight: 900;

            span {
                min-height: 60px;
                display: inline-flex;
                align-items: flex-start;
                padding-right: 80px;
            }
        }

        &__email__title {
            display: inline-flex;
            font-size: 18px;
            line-height: 21px;
            background: url(assets/img/icon-mail.svg) 200px center no-repeat;
            background-size: contain;

            span {
                min-height: 40px;
                background: none;
            }
        }

        &__question__description,
        &__email__description {
            margin: 6px auto 0;
            padding: 20px 0 0;
            border-top: 1px solid $black;
            font-size: 14px;
            line-height: 21px;
        }

        &__question__title {
            font-size: 18px;
            font-weight: 900;
            line-height: 21px;
            max-width: 270px;
            background: url(assets/img/icon-qustion.svg) 100% center no-repeat;
            background-size: contain;
            padding-right: 50px;

            span {
                &:first-child {
                    background: none;
                }
            }
        }

        .btn {
            margin: 24px auto 12px;
        }
    }

    .blog {

        .prev-page {
            order: 1;
        }

        .svg-eye {
            width: 75px;
            height: 74px;
            margin: 0 auto;
            fill: #fff !important;
            order: 2;
        }

        .title_1 {
            order: 3;
        }

        .search-form {
            margin: 0;
            width: 100%;
            order: 4;
        }
    }

    .footer-row {
        display: flex;
        flex-direction: column;

        &__logo {
            width: 100%;
            display: flex;
            justify-content: center;
            padding: 0 10px;
        }

        &__social {
            flex-direction: column;

            ul {
                flex-direction: column;

                li:last-child {
                    a {
                        margin-bottom: 0;
                    }
                }

                a {
                    font-size: 18px;
                    margin-bottom: 18px;
                }
            }
        }

        &__animation {
            top: 140px;
            width: 100px;
        }

        &__social__title {
            font-size: 14px;
            margin-bottom: 18px;
            margin-right: auto;
        }

    }

    .footer-copy {
        flex-direction: column-reverse;
        align-items: center;
        font-size: 14px;
        grid-gap: 0;
        padding-bottom: 10px;
        padding-top: 30px;

        p,
        nav {
            width: 100%;
            max-width: 100%;
            margin: 0 auto;
        }

        li {
            margin-bottom: 23px;
        }
    }

    footer {
        margin-top: 120px;
    }



    /*faq-page*/






    .services-page {}


    /*vacancies*/



    .vacancies-page {


        .page-title {


            &__item {

                p {
                    padding-bottom: 30px;
                    border-bottom: 1px solid $black;
                    width: 100%;
                }




            }

        }
    }

    /*vacancy, search-results*/

    .animate-top__subtitle {
        padding-bottom: 15px;
        border-bottom: 1px solid $black;


    }


    /*faq-page*/



    .faq-page {
        .page-title {

            &__item {
                width: 100%;

                .title_1 {
                    font-size: Min(5vw, 91px);
                    padding: 0 0 0 0;

                    span {
                        margin: 0 0 0 100px;
                        position: relative;

                        &::before {

                            width: 100px;
                            height: 97px;
                            left: 175px;
                            top: -65px;
                        }
                    }
                }


            }

        }
    }



    /*contacts*/


    .contacts {

        .page-title {


            &__item {
                width: 100%;

                .title_1 {
                    padding: 0 0 0 0;

                    &::before {

                        width: 260px;
                        height: 190px;
                        position: absolute;
                        right: -250px;

                    }


                }




            }

        }

    }

    /*price*/


    .price {


        .page-title {

            &__item {
                width: 100%;

                .title_1 {
                    font-size: Min(5vw, 91px);
                    padding: 0 0 0 120px;
                    position: relative;

                    span {
                        margin: 0 0 0 60px;

                    }
                }
            }

            .svg-nice {
                position: absolute;
                top: Min(3vw, 225px);
                left: Min(3vw, 500px);
                width: Min(9vw, 144px);
                z-index: -1;
            }

        }

    }

    /*about*/

    .about {
        .page-title {

            &__item {
                .title_1 {
                    padding: 0;


                }

                p {
                    padding: 0;
                    border-bottom: 1px solid $black;
                    padding-bottom: 30px;
                }

                &:first-child {
                    display: flex;
                    align-items: center;
                    padding-bottom: 70px;
                }

            }



        }

        .before-clinic {

            &__title {

                h2 {

                    max-width: 700px;
                }

                .image-box {

                    top: -40px;
                    max-width: 200px;

                }

            }

            a {

                .image-box {

                    bottom: -20%;

                }
            }

        }

        .school {
            display: flex;
            flex-direction: column;
            align-items: flex-start;

            h2 {
                order: 0;
            }

            p {
                order: 1;
                margin: 0 0 70px;
            }

            .btn {
                order: 2;
                margin: 0 0 50px;
            }

            .image-box {
                width: 100%;
                order: 3;

                a {

                    .alignright {
                        margin-left: auto;
                        margin-right: auto;
                        margin-bottom: 30px;
                        max-width: 100%;
                        width: 100%;
                    }
                }
            }

        }

        .documents {
            display: flex;
            flex-direction: column;
            align-items: flex-start;

            h2 {
                order: 0;
            }

            p {
                order: 1;
                margin: 0 0 70px;
            }

            .btn {
                order: 2;
                margin: 0 0 50px;
            }

            .image-box {
                width: 100%;
                order: 3;

                a {

                    .alignleft {
                        margin-left: auto;
                        margin-right: auto;
                        margin-bottom: 30px;
                        max-width: 100%;
                        width: 100%;
                    }
                }
            }

        }


    }


    /*doctors*/

    .doctors {

        .page-title {
            &__item {

                p {
                    position: relative;
                    margin-bottom: 60px;

                    &:after {
                        content: "";
                        position: absolute;
                        top: calc(100% + 30px);
                        left: 0;
                        width: 100%;
                        border-bottom: 1px solid $black;
                    }

                }



            }
        }

        .doctors__card-one {


            .text-box {

                margin-bottom: 0;

            }

        }

        .doctors__card-two {

            .text-box {

                margin-bottom: 0;


            }

        }

    }

    .pricelist {


        .modal-body {

            padding: 0 Min(12vw, 10rem) 7rem;




        }




    }


    /*forms*/

    .template-form {


        .input-box {
            display: flex;

            .file-upload {
                display: flex;
                align-items: flex-end;
                width: 100%;

                label {
                    display: flex;
                    flex-direction: column;
                }

                input {
                    margin: 0 0 12px;
                }


            }

            .upload-btn {

                label {
                    position: relative;

                    .text-box {
                        font-size: 24px;
                        display: flex;
                        cursor: pointer;
                        top: 17px;
                        left: 20px;
                        position: relative;

                    }

                    img {
                        position: absolute;
                        left: -15px;
                        top: -15px;
                        width: 160px;
                        height: 160px;
                    }
                }
            }
        }
    }

    .table-box {
        overflow-x: auto;
        margin: 60px 0 74px;
    }

    .post-table {
        min-width: 991px;

        tr {
            font-size: 18px;

            &:first-child {
                font-size: 22px;
            }
        }

        td {
            padding: 3px 15px;
        }
    }

    .attention {
        &__img-box {
            width: 285px;
            min-width: 285px;
            margin-right: 45px;
        }

        &__title {
            margin-bottom: 24px;
        }
    }

    .static {
        .page-title {

            margin-bottom: 48px;
        }
    }
    .person_contact {
 
        margin-bottom: 112px;
    }
}
