.first-screen {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;

    .fullscreen-bg {
        width: 100%;
        max-width: 1312px;
        margin: 0 auto;
    }

    .btn {
        max-width: 643px;
        margin: 0 auto;
    }

    .svg-cat {
        position: absolute;
        left: 120px;
        bottom: 0;
        width: 100%;
        height: 165px;
        max-width: 165px;
        fill: #fff;
    }
}
