 // ===========================================

 /* section top */

 .person_content-info_wrapper p {
     width: 100%;
 }

 .location_title {
     font-size: 48px;
     line-height: 64px;
     letter-spacing: 0.1em;
     text-transform: uppercase;
     margin: 50px 0 100px;
     padding-right: 30px;
 }

 .person_contact {
     display: flex;
     justify-content: space-between;
     align-items: center;
     margin-top: -23px;
     margin-bottom: 212px;
 }

 .person_content-info_wrapper {
     display: flex;
 }

 .person_content-info {
     margin-right: 30px;
 }

 .info_item-title {
     font-size: 24px;
     line-height: 36px;
     margin-bottom: 18px;
 }

 .info_item-subtitle {
     font-size: 36px;
     line-height: 48px;
     letter-spacing: 0.14em;
     margin-bottom: 23px;
 }

 .person_content-info_wrapper a {
     color: #000;

     &:hover {
         color: $turquoise;
     }
 }

 .info_item {
     max-width: 600px;
     width: 100%;
     margin-bottom: 100px;
     position: relative;
     padding-bottom: 6px;
 }

 .info_item::after {
     content: '';
     width: 100%;
     height: 1px;
     background-color: #000;
     position: absolute;
     bottom: 8px;
     left: 0;
 }

 .person_content-info:last-child .info_item:last-child::after {
     bottom: -40px;
 }

 .person_contact-right {
     padding: 30px 20px;
     background-color: #74c6c7;
 }

 .right_item {
     border-bottom: 1px solid #000;
     padding-bottom: 30px;
     margin-bottom: 60px;
     display: block;
     color: $black;
    &:last-child{
        border-bottom: none;
    }
     &:hover {
         .right_item-descr {
             color: $black;
             text-decoration: underline;
         }
     }
 }

 .right_item-img {
     width: 75px;
 }

 .right_item-img img {
     width: 100%
 }

 .right_item:last-child {
     margin-bottom: 0;
 }

 .right_item-descr {
     width: 242px;
     font-size: 24px;
     line-height: 36px;
     margin-top: 30px;

 }

 // ====================================

 /*news section */
 section.news {
     margin-top: 100px;
 }

 .news_item {
     display: flex;
     margin-bottom: 100px;
 }

 .news_item-nummber {
     font-size: 72px;
     text-align: right;
     letter-spacing: 0.1em;
     text-transform: uppercase;
     line-height: 1.2;
     width: 350px;
     padding-top: 16px;
     height: 166px;
     background-image: url(assets/img/bg-nummber.png);
     background-position: center;
     background-repeat: no-repeat;
     background-size: contain;
 }

 .news_item-text {
     border-bottom: 1px solid #000;
     padding-bottom: 60px;
     padding-top: 30px;
 }

 .news_item-title {
     font-size: 43px;
     line-height: 64px;
     letter-spacing: 0.1em;
     text-transform: uppercase;
     margin-left: 10px;
 }

 .news_item-date {
     text-align: end;
     margin-bottom: 60px;
     display: flex;
     justify-content: space-between;
 }

 .news_item-subtitle {
     line-height: 36px;
     font-size: 24px;
 }



 // =========================================

 /* not_found section*/

 .not_found-button {
     display: flex;
     justify-content: center;
     margin-top: 90px;
 }

 .not_found-button a {
     letter-spacing: 0.16em;
     text-transform: uppercase;
     font-size: 24px;
     font-weight: 400;
 }

 // ======================================

 /* vacancy section*/

 .single_vacancy {
     .container {
         max-width: 1569px;
     }
 }

 .vacancy {
     margin-top: 150px;

     &_img {
         padding: 0 15px;
     }

     &_item {
         display: flex;
         justify-content: space-between;
         align-items: start;
         margin-bottom: 110px;
     }

     &_item-info {
         max-width: 758px;
         width: 100%;
         border-bottom: 1px solid #000;
         padding: 0 15px 30px;
     }
 }




 .vacancy_item-info_title {
     line-height: 48px;
     letter-spacing: 0.14em;
     text-transform: uppercase;
     font-size: 36px;
     margin-bottom: 60px;
 }

 .vacancy_item-info_sybtitle {
     line-height: 1.2;
     font-size: 24px;
 }

 // ===========================================

 /* requirement section */

 .requirement .container {
     border-bottom: 1px solid #000;
     padding-bottom: 30px;
 }

 .requirement_img img {
     width: 100%;
 }

 .requirement_info {
     display: flex;
     justify-content: space-between;
     margin-top: 60px;
 }

 .requirement_info-title {
     line-height: 64px;
     letter-spacing: 0.1em;
     text-transform: uppercase;
     font-size: 48px;
 }

 .search_form-descr {
     line-height: 36px;
     font-size: 24px;
     font-weight: normal;
     max-width: 702px;
     width: 100%;
     position: relative;
     margin-top: 22px;
 }

 .search_form-descr-list {
     position: relative;
     padding-left: 55px;
 }

 .search_form-descr-list::before {
     content: '';
     position: absolute;
     width: 33px;
     height: 1px;
     background-color: #000;
     top: 18px;
     left: 0px;
 }

 // =================================================

 /* offer section */

 .offer {
     margin-top: 123px;
 }

 .offer_title {
     line-height: 64px;
     letter-spacing: 0.1em;
     text-transform: uppercase;
     font-size: 48px;
 }

 .offer_discr li {
     line-height: 36px;
     font-size: 24px;
     border-bottom: 1px solid #000;
     padding-bottom: 30px;
     margin-top: 60px;
     list-style: none
 }

 .offer_discr li::before {
     display: none;
 }

 .offer_item {
     display: flex;
     justify-content: space-between;
     margin: 123px 0;
 }

 .offer_item-info {
     max-width: 758px;
     width: 100%;
 }

 .offers_item-info_title {
     line-height: 48px;
     letter-spacing: 0.14em;
     text-transform: uppercase;
     font-size: 36px;
     margin-bottom: 30px;
 }

 .offer_item-info_subtitle {
     font-size: 24px;
     line-height: 36px;
     border-bottom: 1px solid #000;
     padding-bottom: 30px;
     margin-bottom: 143px;
 }

 .offer_text-bottom {
     line-height: 36px;
     font-size: 24px;
 }

 .offer_button a {
     line-height: 36px;
     letter-spacing: 0.16em;
     text-transform: uppercase;
     font-size: 24px;
     font-weight: 400;
 }

 .not_fount-img {
     text-align: center;
     margin: -100px 0 0 0;
 }

 @media (max-width: 1560px) {
     .person_contact {
         flex-direction: column;
     }

     .person_contact-right {
         order: 0;
         width: 100%;
         margin-top: 30px;
         margin-right: auto;
         display: flex;
     }

     .right_item {
         display: flex;
         justify-content: center;
         align-items: center;
         padding: 15px;
         margin: 0;
     }

     .right_item-descr {
         width: unset;
         margin-left: 25px;
     }

     .requirement .container {
         justify-content: center;
     }
 }

 @media (max-width: 1410px) {
     .offer_item {
         flex-direction: column;
         align-items: center;
     }

     .offer_item-info {
         margin-top: 20px;
     }

     .offer_button {
         text-align: center;
     }
 }




 @media (max-width: 1280px) {
     .person_content-info_wrapper {
         flex-direction: column;
         justify-content: center;
     }

     .info_item {
         max-width: 100%;
     }

     .person_contact-right {
         flex-direction: column;
         align-items: flex-start;
     }



     .news_item-nummber {
         width: 158px;
     }
 }



 @media (max-width: 1100px) {

     .requirement_info {
         flex-direction: column;
     }

     .search_form-descr {
         width: unset;
         max-width: unset;
     }

     .search_form-descr-list::before {
         left: 0;
     }
 }

 @media (max-width: 768px) {
     .news_item {
         flex-direction: column;
         margin-bottom: 24px;
     }

     .news_item-nummber {
         font-size: 48px;
         width: 59px;
         height: 70px;
         padding-top: 0;
         background-position: right;
     }

     .news_item-title {
         font-size: 24px;
         line-height: 36px;
     }

     .news_item-text {
         padding-bottom: 30px;
         padding: 0;
     }

     .news_item-date {
         margin-bottom: 30px;
         line-height: 1.2;
         font-size: 15px;
     }

     .requirement_info-title,
     .offer_title,
     .offers_item-info_title {
         font-size: 30px;
         line-height: 55px;
     }

     .info_item {
         width: unset;
         max-width: unset;
     }

     .info_item-subtitle {
         font-size: 29px;
         line-height: 35px;
     }

     .person_content-info {
         margin-right: unset;
     }

     .vacancy_img img {
         width: 100%;
     }

     .offers_item-img img {
         width: 100%;
     }

     .right_item-descr {
         font-size: 14px;
         line-height: 21px;
     }

     .right_item-img {
         padding: 5px;
     }

     .right_item {
         padding: 5px 0;
         margin: 0;

         &:last-child {
             margin-bottom: 15px;
         }
     }

     .person_contact-right {
         padding: 5px 10px;
     }

     .right_item-descr {
         margin: 10px 0 10px 34px;
         ;
     }

     .info_item-subtitle {
         font-size: 24px;
         line-height: 29px;
     }

     .info_item {
         margin: 0 auto 30px;
     }

     .location_title {
         font-size: 28px;
         line-height: 40px;
     }



     .news_item-subtitle {
         line-height: 30px;
         font-size: 20px;
     }

     .vacancy_item {
         flex-direction: column;
         margin-bottom: 50px;
     }

     .not_fount-img {
         margin-top: -50px;

     }

     .not_fount-img img {
         margin-top: 0;

     }

     .not_found-button a {
         font-size: 20px;
     }



     .vacancy_item-info_sybtitle {
         font-size: 20px;
     }

     .requirement_info-title,
     .offer_title,
     .offers_item-info_title {
         font-size: 18px;
         line-height: 21px;
         letter-spacing: 0.1em;
         padding-bottom: 10px;
         border-bottom: 1px solid #000;
     }

     .search_form-descr-list,
     .offer_discr li,
     .offer_item-info_subtitle,
     .offer_text-bottom {
         font-size: 14px;
         line-height: 21px;
     }

     .offer_title,
     .offers_item-info_title {
         border: none;
     }

     .offer_discr li {
         padding-bottom: 20px;
         margin-top: 30px;
     }

     .offer_item {
         margin-top: 37px;
         margin: 143px 0 25px;
     }

     .offer_item-info_subtitle {
         margin-bottom: 40px;
     }

     .right_item {
         justify-content: space-between;
     }

     .person_contact-right {
         padding-right: 44px;
     }

     .right_item:last-child {
         border: none;
     }

     .location_title {
         font-size: 24px;
         line-height: 36px;
         margin: 50px 0 30px;
         padding-right: 0;
     }

     .info_item::after {
         bottom: 0;
     }

     .info_item-subtitle {
         line-height: 21px;
         font-size: 18px;
     }

     .news_item-subtitle {
         line-height: 21px;
         font-size: 14px;
     }

     .news_item-date {
         line-height: 21px;
         font-size: 14px;
     }

     .not_found-button a {
         font-size: 14px;
     }

     .vacancy {
         margin-top: 100px;
     }

     .vacancy_item-info_title {
         font-size: 24px;
         margin-bottom: 40px;
         margin-top: 24px;
         line-height: 1.2;
     }

     .vacancy_item-info_sybtitle {
         font-size: 14px;
     }

     .vacancy_img {
         padding: 0;
     }

     .offer {
         margin-top: 50px;

         &_title {
             padding: 0;
         }
     }
 }
