@media screen and (max-width:575px) {
    .first-screen {
        .svg-cat {
            bottom: initial;
            left: calc(100% - 75px);
            width: 75px
        }
    }

    img.alignleft,
    img.alignright,
    figure.alignleft,
    figure.alignright,
    img.aligncenter,
    figure.aligncenter {
        max-width: 100%;
        margin: 12px auto 24px;
    }

    ol li:before {
        margin-left: 7px;
        margin-right: 7px;
        font-size: 14px;
        line-height: 1.5;
    }

    h1,
    .title_1,
    h2,
    .title_2 {
        font-size: 24px
    }

    h2,
    .title_2 {
        font-size: 24px;
        margin-bottom: 20px
    }

    body {
        font-size: 14px;
        line-height: 1.5;
    }

    ul li:before {
        margin-top: 10px;
    }

    .post-table {
        tr {
            height: 72px;
        }
    }

    .page-title {
        &__item {
            .title_1 {
                font-size: 24px
            }

            &:first-child {
                padding-bottom: 40px;

                .prev-page {
                    svg {
                        height: 53px;
                        width: 53px
                    }
                }
            }
        }
    }

    blockquote {
        display: grid;
        width: 100%;
        padding: 24px;
        position: relative;
        margin: 36px auto 48px;

        &::before {
            background-size: 18px;
            margin-left: -28px;
        }

        &::after {
            background-size: 18px;
            margin-right: -40px;
        }
    }

    .services {
        padding: 210px 0 25px;
        margin: 12px -20px;

        &:before,
        &:after {
            width: 100%
        }

        &__item__title {
            font-size: 21px;
            margin: 60px 0 7px;
            padding: 0 25px;
            max-width: calc(100% - 60px);
            position: relative;

            &:before {
                content: "";
                position: absolute;
                top: 9px;
                right: -41px;
                height: 20px;
                width: 65px;
                background: url(assets/img/long-arrow.svg) center no-repeat;
                opacity: 1 !important;
            }
        }

        &__item__description {
            opacity: 1;
            font-size: 14px;
            line-height: 21px;
            color: $black;
            margin-bottom: 20px;
            padding: 0 25px;
            border-bottom: none;
        }

        &__item__img-box {
            img {
                width: 100%
            }
        }

        &__link {
            top: 100px;
            padding-left: 28px;
            left: 140px;

            .title_4 {
                font-size: 18px
            }

            .svg-nice {
                position: absolute;
                top: -97px;
                left: -60px;
                width: 100px
            }

            p {
                display: none
            }
        }
    }

    .circle {
        font-size: 72px;
        margin-left: -10px
    }

    .faq-page {}

    .form-variant {
        h4 {
            font-size: 18px;
            margin: 20px 0 0 0
        }
    }

    .single-doctor {
        &__video-block {
            margin-top: 50px;

            .video-block {
                a {
                    position: relative;

                    .play-button {
                        width: 100px;
                        height: 100px;

                        img {
                            width: 50px;
                            height: 50px
                        }
                    }

                    &::before {
                        display: none
                    }
                }
            }
        }
    }

    .services-page {}

    .vacancies {}

    .single_vacancy {
        .page-title {
            align-items: center
        }

        .single_vacancy__title {
            h5 {
                text-align: left;

                img {
                    width: 64px;
                    height: 58px;
                    z-index: -1;
                    position: relative;
                    left: -33px;
                    top: 3px
                }
            }
        }
    }

    .faq-page {
        .page-title {
            &__item {
                width: 100%;

                .title_1 {
                    padding: 0 0 0 0;

                    span {
                        margin: 0 0 0 60px;
                        position: relative;

                        &::before {
                            left: 110px;
                            top: -45px
                        }
                    }
                }
            }
        }
    }

    .contacts {
        .page-title {
            &__item {
                width: 100%;

                .title_1 {
                    padding: 0 0 0 0;

                    &::before {
                        width: 155px;
                        height: 100px;
                        right: -155px;
                        top: -25px
                    }
                }
            }
        }
    }

    .price {
        .page-title {
            &__item {
                .title_1 {
                    padding: 0 0 0 50px;

                    span {
                        margin: 0 0 0 60px
                    }
                }
            }

            .svg-nice {
                top: 20px;
                left: 20px;
                width: 50px;
                z-index: -1
            }
        }
    }

    .about {
        .page-title {
            &__item {
                .title_1 {
                    &::after {
                        display: none
                    }
                }

                &:first-child {
                    padding-bottom: 40px
                }
            }

            .image-box {
                display: flex;
                width: 100%;
                max-width: 250px;
                margin: 0 auto;
                padding-top: 20px
            }
        }

        section {
            margin-bottom: 50px;

            &:first-child {
                margin-bottom: 50px
            }
        }

        .before-clinic {
            display: flex;
            flex-direction: column;

            .before-clinic {
                &__title {
                    h2 {
                        width: 100%;
                        max-width: 900px;
                        order: 0
                    }

                    .image-box {
                        max-width: 90px;
                        top: -65px;
                        right: 0
                    }
                }
            }

            p {
                order: 1
            }

            a {
                order: 2;

                .alignleft {
                    margin-left: auto;
                    margin-right: auto;
                    margin-bottom: 30px;
                    max-width: 100%
                }

                .image-box {
                    bottom: -10%
                }
            }
        }

        .based {
            display: flex;
            flex-direction: column;

            h2 {
                width: 100%;
                max-width: 590px;
                order: 0
            }

            a {
                order: 2;

                .alignright {
                    margin-left: auto;
                    margin-right: auto;
                    margin-bottom: 30px;
                    max-width: 100%
                }
            }
        }

        .plans {
            display: flex;
            flex-direction: column;

            h2 {
                width: 100%;
                max-width: 590px;
                order: 0
            }

            a {
                order: 2;

                .alignleft {
                    margin-left: auto;
                    margin-right: auto;
                    margin-bottom: 30px;
                    max-width: 100%
                }
            }
        }

        .mission {
            .aligncenter {
                display: none
            }

            &__row {
                &__item {
                    &:first-child {
                        max-width: 100%;
                        margin-bottom: 10px
                    }

                    &:last-child {
                        max-width: 100%;
                        line-height: 48px;

                        p {
                            letter-spacing: 5px
                        }
                    }
                }
            }
        }

        .school {
            p {
                margin: 0 0 30px
            }
        }

        .documents {
            p {
                margin: 0 0 30px
            }
        }
    }

    .doctors {
        .page-title {
            &__item {
                h1 {
                    font-size: 24px
                }
            }
        }

        .doctors__card-one {
            .text-box {
                h3 {
                    margin-bottom: 30px;
                    font-size: 24px
                }

                p:last-child {
                    margin-bottom: 60px
                }
            }
        }

        .doctors__card-two {
            .text-box {
                h3 {
                    margin-bottom: 30px;
                    font-size: 24px
                }

                p:last-child {
                    margin-bottom: 60px
                }
            }
        }
    }

    .modal {
        .template-form {
            margin-top: -115px;

            .form-variant {
                margin-bottom: 0
            }
        }

        .modal-body {
            padding: 3rem 1rem
        }
    }

    .pricelist {
        .template-form {
            margin-top: -36px
        }

        .modal-header {
            .btn-close {
                box-sizing: content-box;
                width: 1em;
                height: 1em
            }
        }

        .modal-body {
            padding: 2rem 2rem;

            h2 {
                max-width: Min(54vw, 600px);
                margin-bottom: 70px;
                position: relative;

                img {
                    max-width: 65px
                }
            }

            p {
                margin-bottom: 50px
            }
        }
    }

    .page-404 {
        .page-title__item {
            .title_1 {
                font-size: Min(5vw, 91px);
                padding: 0
            }
        }
    }
}

@media screen and (max-width:374px) {
    .first-screen .svg-cat {
        display: none
    }

    .form-variant {
        h4 {
            font-size: 18px;
            margin: 20px 0 0 0
        }
    }

    .page-title {
        &__item {
            &:first-child {
                padding-bottom: 40px;

                .prev-page {
                    svg {
                        height: 53px;
                        width: 53px
                    }
                }
            }
        }
    }
}
