.accordion {
    &-title {
        z-index: 0;
        color: $black;
        line-height: 64px;
        text-transform: uppercase;
        font-size: 48px;

        &:hover {
            z-index: 0;
            color: $turquoise
        }

        &:after {
            display: none
        }

        &:focus,
        &:not(.collapsed) {
            background-color: #fff;
            box-shadow: none
        }
    }

    &-item {
        position: relative;
        margin-bottom: 60px;
        padding-left: 84px;
        border: none;
        transition: .3s;
        cursor: pointer;

        .border-left {
            position: absolute;
            z-index: 4;
            left: 50px;
            top: 65px;
            width: 6px;
            height: 45px;
            margin: auto;
            background: transparent;
            content: "";
            transform: translateY(-50%);
            transition: .3s;

            &:before {
                position: absolute;
                left: -8px;
                top: -6px;
                width: 22px;
                height: 22px;
                background-color: $deep-violet;
                border-radius: 50%;
                content: ""
            }

            &:after {
                position: absolute;
                left: -8px;
                bottom: -6px;
                width: 22px;
                height: 22px;
                background-color: $deep-violet;
                border-radius: 50%;
                content: ""
            }
        }

        &.active {
            .border-left {
                top: 50%;
                height: calc(100% - 100px);
                background: $turquoise
            }

            .accordion-body {
                color: $black
            }
        }
    }

    &-body__video-box {
        display: none
    }

    &-body {
        padding: 0 1.25rem;
        font-size: 24px
    }
}
.home__faq-block {
    display: flex;
    padding-bottom: 232px;

    .fullscreen-bg__video {
        display: none;
        max-height: 550px;

        &.active {
            display: block
        }
    }

    > div {
        width: 50%
    }

    &__video {
        text-align: center
    }

    &__video-sticky-box {
        position: sticky;
        top: 24px
    }
}

.faq__video-sticky-box {
    text-align: center;
}