.price-list__item {

    margin-bottom: 86px;

    &-title {
        @extend .title_4;
        @extend .special-container;
    }

    &-list {
        @extend .title_5;
        li{
            &::before{
                display: none;
            }
        }
    }


    .special-container {
        max-width: 1575px;
        padding: 65px 0 31px;
        width: calc(100% - 40px);
        margin: 0 auto;
        border-bottom: 1px solid $black;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .price {
        @extend .title_4;
        margin-bottom: 0;
    }
}
