.list-services {
    margin: 72px auto 200px;
    &__title{
        font-size: 48px;
        margin: 24px 0 36px;
    }
    &__decor-box {
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
    }

    &__item {
        padding: 65px 15px 152px;
        max-width: 992px;
        margin: 0 auto;
        background-repeat: no-repeat;

        &__title {
            display: flex;
            font-size: 48px;
            color: $black;
            display: flex;

            .long-arrow {
                opacity: 0;
            }

            &:hover {
                color: $black;

                .long-arrow {
                    opacity: 1;

                    span {
                        width: 85px
                    }
                }
            }
        }

        &__subtitle {
           
            padding-bottom: 44px;
            border-bottom: 1px solid $black;
        }
    }
}
