.block-about {
    position: relative;
    padding: 250px 15px;
    line-height: 1.2;
    font-size: 3.75vw;

    a {
        z-index: 1;
        display: flex;
        flex-direction: column;
        color: $black;

        &:hover {
            color: $pink;
        }
    }

    span {
        max-width: 780px;
        padding: 0 15px 12px;
        text-transform: uppercase;
    }

    .svg-tooth {
        position: absolute;
        left: 50%;
        top: 50%;
        width: 35vw;
        height: auto;
        transform: translate(-50%, -50%);
    }
    &__bottom-text {
        max-width: 785px;
        margin-left: auto;
        text-align: right;
    }
}