.doctors__card-one{
	display: flex;
	align-items: flex-end;
	justify-content: space-between;
	margin-top: -60px;

	.text-box{
		width: 100%;
		max-width:  760px;
		position: relative;
		margin-bottom: 130px;
		h3{
			margin-bottom: 60px;
			width: 100%;
			max-width: 505px;
			a {
				color: #000;
				&:hover{
					color: $turquoise;
				}
			}
		}
		p:last-child{
			margin-bottom: 60px;
		}
		&::after{
			content: "";
			position: absolute;
			width: 100%;
			border-bottom: 1px solid $black;
			bottom: -0px;
		}
	}
	.image-box{
		margin-right: 60px;
	}
}
.doctors__card-two{
	display: flex;
	align-items: flex-end;
	justify-content: space-between;
	margin-top: -60px;

	.text-box{
		width: 100%;
		max-width:  600px;
		position: relative;
		margin-bottom: 130px;

		text-align: right;
		.title_3{
			margin-bottom: 60px;
			width: 100%;
			a {
				color: #000;
				&:hover{
					color: $turquoise;
				}
			}
		}
		p:last-child{
			margin-bottom: 60px;
		}
		&::after{
			content: "";
			position: absolute;
			width: 100%;
			border-bottom: 1px solid $black;
			bottom: -0px;
			left: 0;
		}
	}
	.image-box{
		width: 100%;
		max-width: 758px;
		margin-left: 60px;
	}
}
