svg.svg-eye {
    fill: $white !important;
}

.blog {
    .font-weight-light {
        padding-left: 72px;
    }

    .search-form {
        margin: auto 0 auto auto;
        max-width: 444px;
        width: 100%;
    }
}


.blog-navigation {
    font-size: 24px;
    line-height: 36px;
    text-align: right;
    position: relative;
    z-index: 1;
    max-width: 900px;
    margin: -100px 0 100px auto;

    a {
        margin-right: 35px;
        color: $black;
        white-space: nowrap;
        line-height: 1.5;

        &.active {
            font-weight: 900;
        }

        &:hover {
            color: $turquoise;
        }

        &:last-child {
            margin-right: 0;
        }
    }
}

.article-list {
    @include container-sm;
    display: flex;
    flex-wrap: wrap;


    &__item {
        display: flex;
        flex-direction: column;
        margin-top: 24px;
        padding-bottom: 12px;
        margin-bottom: 100px;
        // width: 50%;
        padding: 0 50px;
        // &:nth-child(3n + 1) {
        //     width: 100%;
        // }

        &:hover {
            .article-list__item__title {
                text-decoration: underline;
            }
        }
    }

    &__item__img {
        display: flex;
        background-size: cover;
        background-position: center center;
        padding-top: 60%;
        width: 100%;
    }

    &__item__title {
        width: 100%;
        @extend .title_3;
        margin: 48px auto 60px;
    }

    &__item__category {
        margin-top: auto;
        display: flex;
        @extend .title_5;
        .date{
            margin-left: auto;
        }
    }

    &__item__date {
        @extend .title_5;
        margin-left: auto;
    }
}
