.faq-page {
    .page-title {
		align-items: flex-start;
        &__item {
            width: 100%;

            .title_1 {
                font-size: Min(5vw, 91px);
                padding: 0 0 0 50px;

                span {
                    margin: 0 0 0 Min(35vw, 505px);
                    position: relative;

                    &::before {
                        content: "";
                        background-image: url(assets/img/svg-flower.svg);
                        background-size: cover;
                        width: 205px;
                        height: 200px;
                        position: absolute;
                        left: -180px;
                        top: -110px;
                        z-index: -1;
                    }
                }
            }


        }

    }
    .home__faq-block{
        background-image: url(assets/img/faq-bg.png);
        background-repeat: no-repeat;
        background-position: center right;
        background-size: contain;
        > div {
            width: 100%;
            max-width: 768px;
        }
    }

    .faq__video{
        display: none;
    }
}

.section-accordion-title {
    font-size: 36px;
    margin-bottom: 24px;
}

.home__faq-block {
    display: flex;
    padding-bottom: 72px;
}

.faq-page__accordion {
    display: flex;
    flex-direction: column;
    margin-bottom: 100px;

    &:first-child {
        background-image: url(assets/img/faq-bg.png);
        background-repeat: no-repeat;
        background-position: center right;
        background-size: contain;
    }

    h4 {
        padding-left: 35px;
        margin-bottom: 60px;

    }

    .fullscreen-bg__video {
        display: none;
        max-height: 550px;

        &.active {
            display: block
        }
    }

    >div {
        width: 70%
    }

    &__video {
        text-align: center
    }

    &__video-sticky-box {
        position: sticky;
        top: 24px
    }
}

.bottom-animation {
    margin-bottom: 100px;
}
