.modal {
    &-backdrop.show {
        opacity: .8;
        background-color: #74C6C7
    }

    &.map {
        .modal-header {
            padding: 24px 3rem 36px;
        }

        .modal-body {
            padding: 0 3rem 3rem;
        }

        .modal-dialog {
            max-width: 1400px;
        }
    }

    iframe {
        width: 100%;
        height: 66vh;
    }

    .template-form {
        margin-top: -85px;

        .form-variant {
            margin-bottom: 40px
        }


    }

    .modal-dialog {
        max-width: 1770px
    }

    &-header {
        border-bottom: 1px solid transparent;

        .btn-close {
            box-sizing: content-box;
            width: 3em;
            height: 3em;
            padding: .25em .25em;
            background: transparent url(assets/img/close-btn.png) center/3em auto no-repeat;
            border: 0;
            border-radius: 0;
            opacity: .5;

            &:focus {
                box-shadow: none
            }
        }
    }

    .modal-body {
        padding: 3rem
    }

    .modal-content {
        border: 1px solid transparent;
        border-radius: 0
    }
}

.pricelist {
    .modal-dialog {
        max-width: 900px;
        max-height: 850px
    }

    .modal-body {
        padding: 0 Min(5vw, 5rem) 3rem;

        h2 {
            max-width: Min(35vw, 600px);
            margin-bottom: 100px;
            position: relative;

            img {
                position: absolute;
                max-width: Min(10vw, 130px);
                max-height: 95px;
                overflow: hidden
            }
        }

        p {
            max-width: 600px;
            position: relative;
            margin-bottom: 100px;

            &::before {
                content: "";
                position: absolute;
                border-bottom: 1px solid #000;
                width: 100%;
                left: 0;
                top: -40px
            }
        }

        .btn-box {
            display: flex;
            justify-content: center;
            width: 100%;
        }
    }

    .modal-content {
        border: 1px solid transparent;
        border-radius: 0
    }

    .template-form label,
    .template-form .hello,
    .template-form input {
        white-space: nowrap;
        font-size: 24px;
        margin-top: 20px;
    }

    [type="submit"] {
        border: none;
    }

}

.resume {
    .template-form {
        position: relative;

        input,
        textarea {
            background-color: transparent;
            z-index: 3
        }
    }
}

.btn-primary:focus {
    box-shadow: none
}




/*form file*/
.input-file {
    
    color: #878787;
    padding: 0;
    background-image: url(assets/img/icon-download.png);
    background-size: 24px;
    background-repeat: no-repeat;
    background-position: 18px;
    cursor: pointer;
}

.input-box {
    .input-file {
        width: 100%;
        padding: 0 60px 0 10vw;
        border: none;
        height: 48px;
        border-bottom: 1px solid #000;
        font-size: 24px;
        margin-left: 25px;
        margin-top: 28px;
        color: #a5a5a5;
        font-weight: 300;
        &:hover{
            opacity: .5;
        }
    }
}

.input-file::-webkit-file-upload-button {
    background-color: transparent;
    border: none;
    color: $black;
    cursor: pointer;
    font-size: 0px;
    outline: none;
    padding: 0;
    text-transform: uppercase;
    transition: all 1s ease;
    margin-left: auto;
    text-align: center;
}

