// mixins
@mixin square($size, $radius: 0) {
    width: $size;
    height: $size;

    @if $radius !=0 {
        border-radius: $radius;
    }
}

@mixin exactSizes ($width, $height) {
    width: $width;
    height: $height;
}

@mixin sizeBlock ($m-width, $height) {
    height: $height;
    max-width: $m-width;
    width: 100%;
}

@mixin fontEl($fontSize, $lineheight) {
    font-size: $fontSize;
    line-height: $lineheight;
}

%lastChild {
    &:last-child {
        margin-bottom: 0;
    }
}

// end mixins
.wrapper-nasty {
    .holder {
        width: 100%;
        max-width: 1585px;
        margin: 0 auto;
    }

    h2,
    h3 {
        text-transform: uppercase;
        letter-spacing: 0.1em;
    }

    p {
        @extend %lastChild;
    }

    .section-top {
        padding-top: 128px;
        padding-bottom: 127px;
    }

    .slider-advantages {
        .block {
            &-img {
                max-width: 757px;
                max-height: 975px;

                img {
                    width: 100%;
                }
            }

            &-info {
                max-width: 600px;
                margin-left: auto;
            }
        }

        .number {
            @include fontEl(96px, 100.5px);
            font-style: italic;
            margin-bottom: 60px;
        }

        h3 {
            padding-bottom: 30px;
            border-bottom: 1px solid $black;
            margin-bottom: 30px;
        }

        .slick-dots {
            position: absolute;
            top: 50%;
            left: -57px;
            transform: translate(-100%, -50%);
            list-style: none;
            padding-left: 0;
            margin-bottom: 0;

            li {
                @include square(74px, 50%);
                align-items: center;
                justify-content: center;
                margin-bottom: 48px;
                @extend %lastChild;

                &:before {
                    content: none;
                }

                &.slick-active {
                    border: 1px solid $deep-violet;
                }
            }

            button {
                @include square(22px, 50%);
                background-color: $deep-violet;
                border: 0;
                padding: 0;
                font-size: 0;
            }
        }
    }

    .section-get {
        padding-top: 128px;
        padding-bottom: 151px;

        .get-list {
            padding-top: 30px;
            padding-bottom: 30px;
            position: relative;

            &:after,
            &:before {
                content: '';
                position: absolute;
                top: 0;
                left: 12px;
                width: 100%;
                height: 1px;
                background-color: $black;
            }

            &:after {
                top: 100%;
            }

            .block {
                &-img {
                    width: 285px;
                }

                &-text {
                    width: calc(100% - 285px);
                    padding-left: 30px;

                    &__title {

                        margin-bottom: 15px;
                    }
                }
            }
        }
    }

    .section-doctors {
        padding-top: 64px;
        padding-bottom: 64px;

        h2 {
            max-width: 1150px;
        }

        .slider-doctors {
            margin-left: 158px;
            padding-right: 168px;

            &:after {
                content: '';
                position: absolute;
                top: 50%;
                right: 0;
                transform: translateY(-50%);
                @include exactSizes(80px, 116px);
                background: url(assets/img/arrows-slider-bg.svg) no-repeat 0 0;
                background-size: contain;
                margin-top: 27px;
                z-index: -1;
            }

            &__item {
                align-items: flex-end;
                padding-right: 30px;

                .block {
                    &-img {
                        width: 285px;
                    }

                    &-text {
                        width: calc(100% - 285px);
                        padding-left: 30px;
                        padding-bottom: 30px;
                        position: relative;

                        &:after {
                            content: '';
                            position: absolute;
                            bottom: 0;
                            left: 30px;
                            width: calc(100% - 30px);
                            height: 1px;
                            background-color: $black;
                        }

                        &__title {
                            max-width: 285px;
                            margin-bottom: 30px;
                        }

                        p {
                            @include fontEl(16px, 24px);
                            margin-bottom: 0;
                        }
                    }
                }
            }

            .slick {
                &-arrow {
                    position: absolute;
                    top: 50%;
                    right: 17px;
                    transform: translateY(-50%);
                    @include sizeBlock(103px, 23px);
                    font-size: 0;
                    padding: 0;
                    left: inherit;
                    border: 0;
                    background: url(assets/img/arrows-icon.svg) no-repeat 0 0;
                    background-size: cover;
                }

                &-prev {
                    transform: rotate(180deg);
                    margin-top: 35px;
                }
            }
        }

        .btn-wrap {
            margin-top: 81px;
        }

        .btn-bg {
            display: inline-flex;
            align-items: center;
            justify-content: center;
            min-width: 494px;
            height: 112px;
            @include fontEl(24px, 36px);
            letter-spacing: 0.16em;
            text-transform: uppercase;
            background: url(assets/img/btn-bg.svg) no-repeat 0 0;
            background-size: contain;
        }
    }

    .section-address {
        padding-top: 130px;
        padding-bottom: 99px;

        .responsive-tabs {
            .nav-tabs {
                display: block;
                padding-left: 37px;
                border-bottom: 0;

                .nav-link {
                    width: 100%;
                    @include fontEl(36px, 48px);
                    letter-spacing: 0.14em;
                    color: $black;
                    border: 0;
                    border-radius: 0;
                    border-bottom: 1px solid $black;
                    padding: 30px 100px 47px 0;
                    margin-bottom: 0;
                    position: relative;

                    &:after {
                        content: '';
                        position: absolute;
                    }

                    &:after {
                        display: inline-block;
                        top: 50%;
                        right: 25px;
                        @include sizeBlock(103px, 23px);
                        background: url(assets/img/arrows-icon.svg) no-repeat 0 0;
                        background-size: contain;
                        transform: translateY(-50%);
                        opacity: 0;
                        transition: 0.15s ease-in-out;
                    }

                    .active-bg {
                        position: absolute;
                        top: 0;
                        left: -99%;
                        width: 100%;
                        height: 100%;
                        background-color: inherit;
                        z-index: -1;
                    }

                    &.active {
                        background-color: #74C6C7;

                        &:after {
                            opacity: 1;
                        }
                    }
                }
            }

            .dots-icon {
                display: inline-block;
                @include square(22px, 50%);
                background-color: $deep-violet;
            }

            .card {
                border: none;

                &-header {
                    display: none;
                }

                .collapse {
                    display: block;
                }
            }

            .contact-list {
                &__item {
                    display: block;
                    margin-bottom: 100px;
                    @extend %lastChild;
                }
            }

            .card {
                &-body {
                    @include fontEl(36px, 48px);
                    letter-spacing: 0.14em;
                    color: $black;
                    padding-left: 164px;

                    h5 {
                        margin-bottom: 30px;
                    }

                    a {
                        color: $black;
                        &:hover{
                            color: $turquoise
                        }
                    }

                    p {
                        @include fontEl(36px, 48px);
                        letter-spacing: 0.14em;
                    }
                }
            }
        }
    }

    .section-values {
        padding-top: 114px;
        padding-bottom: 65px;

        h2 {
            text-align: right;
            margin-bottom: 140px;

            span {
                position: relative;

                &:before {
                    content: '';
                    position: absolute;
                    top: -49px;
                    left: -41px;
                    display: inline-block;
                    @include sizeBlock(186px, 209px);
                    background: url(assets/img/heart-icon.svg) no-repeat 0 0;
                    background-size: cover;
                    transform: translateX(-100%);
                }
            }
        }

        .values-list {
            padding-left: 200px;

            &__item {
                position: relative;
                margin-bottom: 94px;
                @extend %lastChild;

                img {
                    position: absolute;
                    top: 0;
                    left: 0;
                    transform: translateX(-100%);
                }

                h3 {
                    line-height: 56px;
                    letter-spacing: 0.105em;
                    margin-bottom: 30px;
                }

                p {
                    max-width: 910px;
                }

                &:first-child {
                    img {
                        top: -35px;
                        left: 33px;
                    }
                }

                &:nth-child(2) {
                    img {
                        top: 10px;
                        left: 48px;
                    }
                }

                &:nth-child(3) {
                    img {
                        top: -4px;
                        left: 17px;
                    }
                }

                &:nth-child(4) {
                    img {
                        top: 28px;
                        left: 14px;
                    }
                }
            }
        }
    }

    // media
    // @media (min-width: 992px) {
    //     .section-address {
    //         .responsive-tabs {
    //             .nav-tabs {
    //                 display: block;
    //                 padding-left: 37px;
    //                 border-bottom: 0;
    //             }
    //             .card {
