    @media screen and (max-width: 1859px) {
        .slider-advantages {
            .slick-dots {
                top: 100%;
                left: 50%;
                transform: translate(-50%, 0);
                display: flex;
                margin: 30px auto 0;

                li {
                    margin-bottom: 0;
                    margin-right: 64px;

                    &:last-child {
                        margin-right: 0;
                    }
                }
            }
        }
    }

    @media screen and (max-width: 1439px) {
        .section-doctors {
            .slider-doctors {
                margin-left: 0;

                &__item {
                    .block {
                        &-img {
                            width: 150px;
                        }

                        &-text {
                            width: calc(100% - 150px);
                        }
                    }
                }
            }
        }

        .section-address {
            .responsive-tabs {
                .card {
                    &-body {
                        padding-left: 65px;
                    }
                }
            }
        }
    }

    @media screen and (max-width: 1199px) {
        .section-get {
            .get-list {
                .block {
                    &-img {
                        width: 150px;
                    }

                    &-text {
                        width: calc(100% - 150px);
                        padding-left: 30px;
                    }
                }
            }
        }

        .section-doctors {
            .slider-doctors {
                padding-right: 140px;
            }
        }

        .section-address {
            .responsive-tabs {
                .nav-tabs {
                    .nav-link {
                        &:after {
                            width: 70px;
                        }
                    }
                }
            }
        }

        .section-values {
            .values-list {
                &__item {
                    &:first-child {
                        img {
                            top: -14px;
                        }
                    }

                    &:nth-child(2) {
                        img {
                            top: 24px;
                        }
                    }
                }
            }
        }
    }

    @media screen and (max-width: 991px) {
        .section-top {
            padding-top: 31px;
            padding-bottom: 26px;
        }

        .slider-advantages {
            .block {
                &-img {
                    width: 100%;
                    max-width: 100%;
                    max-height: 53.3vw;
                    overflow: hidden;
                }

                &-info {
                    max-width: inherit;
                    padding-top: 66px;

                    p {
                        line-height: 21px;
                    }
                }
            }

            .number {
                @include fontEl(48px, 62px);
                margin-bottom: 30px;
            }

            h3 {
                @include fontEl(18px, 21px);
            }

            .slick-dots {
                top: 53.3vw;

                li {
                    @include square(36px, 50%);
                }

                button {
                    @include square(11px, 50%);
                }
            }
        }

        .section-get {
            padding-top: 26px;
            padding-bottom: 29px;

            .get-list {
                &-item {
                    padding-bottom: 30px;
                    border-bottom: 1px solid $black;
                    margin-bottom: 30px;

                    &:last-child {
                        padding-bottom: 0;
                        border-bottom: 0;
                        margin-bottom: 0;
                    }
                }

                .block {
                    &-img {
                        width: 285px;
                    }

                    &-text {
                        width: calc(100% - 285px);
                        padding-left: 30px;

                        &__title {
                            @include fontEl(18px, 21px);
                            text-transform: uppercase;
                            margin-bottom: 30px;
                        }

                        p {
                            line-height: 21px;
                        }
                    }
                }
            }
        }

        .section-doctors {
            padding-top: 23px;
            padding-bottom: 31px;

            .slider-doctors {
                padding-right: 0;
                padding-bottom: 53px;

                &:after {
                    content: none;
                }

                &__item {
                    padding-right: 0;

                    .block {
                        &-img {
                            width: 220px;
                        }

                        &-text {
                            width: calc(100% - 220px);
                        }
                    }
                }

                .slick {
                    &-arrow {
                        top: 100%;
                        right: 0;
                        transform: translateY(-23px);
                    }

                    &-prev {
                        right: unset;
                        left: 0;
                        margin-top: 0;
                        transform: translateY(-23px) rotate(180deg);
                    }
                }
            }

            .btn-wrap {
                margin-top: 57px;
            }

            .btn-bg {
                min-width: 247px;
                height: 54px;
                @include fontEl(14px, 24px);
                padding: 8px 16px;
            }
        }

        .section-address {
            padding-top: 20px;
            padding-bottom: 20px;

            .responsive-tabs {
                .tab-pane {
                    display: block !important;
                    opacity: 1;

                    &:first-child {
                        .card-header {
                            background-color: #74C6C7;

                            &:before {
                                content: '';
                                position: absolute;
                                top: -1px;
                                left: 20px;
                                width: calc(100% - 40px);
                                height: 1px;
                                background-color: $black;
                            }
                        }
                    }

                    &:nth-child(2) {
                        .card-header {
                            background-color: #BACF39;
                        }
                    }

                    &:nth-child(3) {
                        .card-header {
                            background-color: #8D7DB9;
                        }
                    }
                }

                .nav-tabs {
                    display: none;
                }

                .dots-icon {
                    @include square(10px, 50%);
                }

                .card {
                    &-header {
                        display: block;
                        padding: 12px 20px 28px;
                        border-radius: 0;
                        margin: 0 -20px;
                        position: relative;

                        &:after {
                            content: '';
                            position: absolute;
                            bottom: -1px;
                            left: 20px;
                            width: calc(100% - 40px);
                            height: 1px;
                            background-color: $black;
                        }

                        h4 {
                            a {
                                color: $black;
                            }
                        }

                        .arrow-icon {
                            display: block;
                            @include sizeBlock(61px, 14px);
                            background: url(assets/img/arrows-icon.svg) no-repeat 0 0;
                            background-size: contain;
                            margin-left: auto;
                            margin-top: -14px;
                        }
                    }

                    &-body {
                        padding: 0;

                        h5 {
                            @include fontEl(14px, 21px);
                        }
                    }

                    .collapse {
                        display: none;

                        &.show {
                            display: block;
                        }
                    }
                }

                .contact-list {
                    &__item {
                        @include fontEl(18px, 21px);
                        letter-spacing: 0.1em;
                        padding-top: 30px;
                        padding-bottom: 20px;
                        border-bottom: 1px solid $black;
                        margin-bottom: 0;

                        p {
                            @include fontEl(18px, 21px);
                            letter-spacing: 0.1em;
                        }
                    }
                }
            }
        }

        .section-values {
            padding-top: 61px;
            padding-bottom: 28px;

            h2 {
                text-align: left;
                margin-bottom: 32px;

                span {
                    &:before {
                        top: -29px;
                        left: 100%;
                        @include exactSizes(84px, 95px);
                        transform: translateX(0);
                    }
                }
            }

            .values-list {
                padding-left: 0;

                &__item {
                    margin-bottom: 50px;

                    img {
                        width: 20%;
                        max-width: 75px;
                        max-height: 97px;
                        position: static;
                        transform: unset;
                    }

                    h3 {
                        width: 80%;
                        @include fontEl(14px, 21px);
                        margin-bottom: 0;
                    }
                }
            }
        }
    }

    @media screen and (max-width: 575px) {
        .section-get {
            .get-list {
                .block {
                    &-img {
                        width: 100%;
                        margin-bottom: 30px;

                        img {
                            width: 100%;
                        }
                    }

                    &-text {
                        width: 100%;
                        padding-left: 0;
                    }
                }
            }
        }

        .section-doctors {
            .slider-doctors {
                &__item {
                    .block {
                        &-text {
                            width: 100%;
                            padding-left: 0;
                            &:after {
                                left: 15px;
                            }
                        }
                    }
                }
            }
        }
    }
}
