.feedback {
    @include container;
    grid-gap: 200px;
    margin-top: 200px;
    display: flex;

    &__question,
    &__email {
        display: flex;
        flex-direction: column
    }

    &__email__title {
        @extend .title_2;
        display: grid;

        span {
            text-align: right;
            padding-right: 148px;
            background: url(assets/img/icon-mail.svg) right center no-repeat;
            background-size: contain;
        }
    }

    &__question__title {
        @extend .title_2;
        display: grid;

        span {
            width: 100%;


            &:first-child {
                background: url(assets/img/icon-qustion.svg) 390px center no-repeat;
                background-size: contain;
            }

            &:last-child {
                text-align: right;
            }
        }
    }

    &__question__description,
    &__email__description {
        margin-top: auto;
    }

    .btn {
        display: flex;
        margin: 56px auto 12px;
    }

  
}
