.about {
    overflow: hidden;


    .page-title {

        &__item {
            .title_1 {
                font-size: Min(5vw, 91px);
                padding: 0 0 0 50px;
                position: relative;

                &::after {
                    position: absolute;
                    content: "";
                    background: url(assets/img/about-title-vector.png) no-repeat;
                    background-size: contain;
                    width: 290px;
                    height: 300px;
                    z-index: -1;
                    top: -55px;
                    right: -230px;

                }
            }

           


        }


        /*замена :after - станет видиммым на 574*/

        .image-box {
            display: none;
            width: 100%;
            max-width: 250px;
            margin: 0 auto;
        }

    }

    section {
        margin-bottom: 200px;

        &:first-child {
            margin-bottom: 150px;
        }
    }




}




.before-clinic {
    position: relative;

    &__title {
        display: flex;
        justify-content: space-between;
        flex-direction: row;
        position: relative;

        h2 {
            width: 100%;
            max-width: 900px;
        }

        .image-box {
            position: absolute;
            right: -50px;
            width: 100%;
            max-width: 270px;
            z-index: -1;
        }
    }


    a {

        .alignleft {
            margin: 0 190px 0 0;
        }

        .image-box {
            position: absolute;
            z-index: -1;
            bottom: -50%;
            right: 50%;
            width: 100%;
            max-width: 955px;
        }
    }


}


.based {

    h2 {
        width: 100%;
        max-width: 590px;
    }

    .alignright {
        margin: 0 0 0 35px;
    }
}

.plans {
    position: relative;

    .alignleft {
        margin: 0 120px 0 0;
    }

    .image-box {
        position: absolute;
        top: Min(30vw, -100px);
        right: Min(30vw, -100px);
        z-index: -1;
        width: 100%;
        max-width: Min(30vw, 650px);
    }
}

.mission {
    .aligncenter {
        margin-bottom: 100px;
    }

    &__row {
        display: flex;
        justify-content: space-between;
        margin: 0 auto;

        &__item {
            &:first-child {
                max-width: 590px;
            }

            &:last-child {
                font-size: Min(3vw, 36px);
                max-width: 760px;
                line-height: 48px;

                p {
                    letter-spacing: 10px;
                }
            }
        }
    }
}

 
.documents {
	.image-box{

		a{

			.alignleft{
				margin: 0 100px 100px 0;
			}
		}
	}
	 
}


