header {
    display: flex;
    align-items: center;
    padding-bottom: 34px;
    padding-top: 34px;

    a {
        color: $black;
        font-size: 36px;

        &:hover {
            color: $light-green;
        }

        &.header-nav-callback {
            margin-left: 0;
            padding-left: 0;
        }
    }
}

.header-logo {
    min-width: 360px;
}

.header-contacts {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 470px;
    margin-left: auto;
    padding: 0 30px;
    text-align: center;

    span {
        letter-spacing: 1.5px;
        text-transform: uppercase;
        white-space: nowrap;
        font-size: 16px;
    }
}

.header-nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 550px;
    padding-left: 15px;

    a {
        padding: 0 15px;
    }
}

.header-nav__language {
    display: flex;

    a {
        &:first-child {
            border-right: 1px solid $black;
        }

        &.active {
            font-weight: 600;
        }
    }
}

.header-nav__callback {
    position: relative;
    display: flex;
    align-items: center;
    margin: 0 15px;

    .svg-zubik {
        position: absolute;
        z-index: -1;
        right: -50px;
        top: -10px;
        min-width: 60px;
    }
}

[data-animate] {
    opacity: 0;
    transition: all 0.2s;
}

.header-nav__menu {
    position: absolute;
    z-index: -1;
    right: -50px;
    top: -20px;
    width: 0;
    height: 0;
    padding: 7.813vw 11.458vw;
    border-radius: 50%;
    display: none;
    align-items: center;

    .header-nav__menu-overlay {
        position: absolute;
        z-index: -1;
        right: 0;
        top: 0;
        width: 0;
        height: 0;
       
        content: "";
        transition: 0.4s;
    }

    &.show {
        display: flex;
        position: fixed;
        z-index: 7;
        right: 0;
        top: 0;
        width: 100vw;
        height: 100vh;
        border-radius: 0;

        .header-nav__menu-overlay {
            width: 200vw;
            height: 200vw;
            animation-fill-mode: forwards;
            transition: 0.4s;
            transform: translate(25%, -25%);
            animation: overlay .1s ease-in-out forwards;
        }
    }
}
@keyframes overlay{
    0% {
        background: #bacf3900;
    }
 

    100% {
        background: #bacf39;
    }
}
.menu-contacts {
    position: relative;
    margin: 36px 0 auto 95px;
    width: 50%;
    // svg {
    //     position: absolute;
    //     left: 0;
    //     top: 0;
    // }

    .socials {
        position: relative;
        margin-bottom: 115px;
        &::before {
            position: absolute;
            left: -65px;
            top: -85%;
            width: 780px;
            height: 773px;
            background-image: url("data:image/svg+xml,%3Csvg  viewBox='0 0 782 775' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.7051 1C-17.4514 134.304 -7.8941 392.829 271.587 360.502C620.939 320.093 711.408 735.331 630.681 765.986C549.955 796.642 536.036 735.331 561.089 650.333C586.143 565.334 711.408 466.402 781 605.743' stroke='black'/%3E%3C/svg%3E");
            content: "";
            z-index: -1;
        }
    }
}

.header-link {
    display: flex;
    align-items: center;
    width: 50%;
    z-index: 1;
    height: 100%;

    ul {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    li {
        margin-bottom: 1.2vw;
        &:before {
            display: none;
        }


        a {
            display: flex;
            align-items: center;
            line-height: 1;
            font-size: Min(7.5vw, 60px);

            &:hover {
                color: $pink;
            }

            &:after {
                z-index: 1;
                display: inline-block;
                width: 22px;
                height: 22px;
                margin-top: 4px;
                margin-left: 28px;
                background-color: $pink;
                border-radius: 50%;
                content: "";
            }
        }
    }
}

.header-nav__menu-close {
    position: absolute;
    z-index: 2;
    right: 70px;
    top: 50px;
    width: 62px;
    height: 62px;
    cursor: pointer;
}

.address {
    max-width: 450px;
}