$violet: #8D7DB9;
$turquoise: #74C6C7;
$light-green:#BACF39;
$pink: #EF7D9A;
$deep-violet: #413D58;
$black: #000;
$grey: #A5A5A5;
$white: #fff;

$font-primary: 'SourceCodePro-Regular';
$font-secondary: 'SourceCodePro-ExtraLight';

::selection {
    color: $pink;
    background: $black;
}

@mixin container {
    max-width: 1850px;
    padding-right: var(--bs-gutter-x, 1.25rem);
    padding-left: var(--bs-gutter-x, 1.25rem);
    margin-right: auto;
    margin-left: auto;
}

@mixin container-sm {
    max-width: 1585px;
    padding-right: var(--bs-gutter-x, 1.25rem);
    padding-left: var(--bs-gutter-x, 1.25rem);
    margin-right: auto;
    margin-left: auto;
}

@mixin scroll {
    /* IE */
    scrollbar-face-color: $pink;
    scrollbar-shadow-color: $light-green;
    scrollbar-highlight-color: $light-green;
    /* FireFox */
    scrollbar-color: $pink $light-green;
    overflow-y: auto;
    overflow-x: hidden;
    padding-right: 10px;

    &::-webkit-scrollbar {
        width: 6px;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $pink;
        background-clip: padding-box;
        border: 0px solid $light-green;

        &:hover {
            background-color: $turquoise;
            cursor: pointer;
        }
    }

    &::-webkit-scrollbar-track {
        background-color: $light-green;
        cursor: pointer;
    }

    ::-webkit-scrollbar-button {
        background-color: transparent;
    }
}

%lastChild {
    &:last-child {
        margin-bottom: 0;
    }
}
