.bottom-animation {
    position: relative;
    padding-left: Min(16.5vw, 115px);
   a{
    color: #000;
    &:hover{
        color: $turquoise;
    }
   }
    &::before {
        content: "";
        display: block;
        width: calc(100% - 340px);
        border-top: 1px solid #000;
        position: absolute;
        right: 20px;
        top: 66%;
    }

    .svg-cat {
        max-width: Min(18.5vw, 165px);
    }

    a {
        position: absolute;
        transform: rotate(-90deg);
        top: Min(15.5vw, 81px);
        left: Min(0.5vw, 50px);
        font-size: Min(2.5vw, 16px);
    }
}

.page-title {
    display: flex;
    padding: Min(4vw, 87px) 15px 0;
    position: relative;
    max-width: 2114px;
    width: 100%;
    margin: 0 auto;
    &__item {
		width: 50%;

        .title_1 {
            font-size: Min(5vw, 91px);
            padding: 0 250px 0 50px;

            span {
                margin: 0 0 0 120px;
            }
        }
        p {
            padding: 100px 0 0 50px;
			width: 100%;
        }

		&:first-child{
			display: flex;
			align-items: flex-start;
		}

    }

}
