
.zigzag {
    max-width: 197px;
    fill: $white;
}

.adults-territory {
    position: relative;
    padding: 16px 0 67px;

    &:after {
        position: absolute;
        right: 0;
        top: 0;
        width: 90%;
        height: 1px;
        background-color: $black;
    }

    &:after {
        top: inherit
    }

    &__title {
        margin-bottom: 106px;
        margin-left: 10%;
        font-size: 36px;
    }

    &__item {
        margin: auto 33% auto auto;
        transition: .1s;
    }
}

