.wp-pagenavi {
    display: flex;
    align-items: center;
    justify-content: center;

    a {
        color: $black;
        margin: 0 12px;
        &:hover{
            color: $turquoise
        }
    }

    .first,
    .previouspostslink,
    .last,
    .nextpostslink {
        margin: 0 12px;
        font-size: 60px;
        font-family: monospace;position: relative;
        top: -4px;
    }

    .extend {
        font-size: 0;
        display: flex;
        align-items: center;


        &:before {
            content: " ";
            width: 102px;
            height: 1px;
            background: $black;
            display: flex;
            align-items: center;
        }
    }

    .current {
        font-size: 36px;
        margin: 0 10px;
    }
}
