.template-map {
    position: relative;
    display: flex;
    align-items: center;
    min-height: 550px;
    margin-top: 200px;
    padding: 15px 0;
    background: url(assets/img/bg-map.jpg) center no-repeat;
    background-size: cover;

    &__custom-border {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        min-height: 332px;
        max-width: 550px;
        margin-left: 188px;
        padding: 72px 0;
        background: #ffffff57;
        border-radius: 45% 55% 64% 36% / 34% 33% 67% 66%
    }

    &__special-box {
        width: 100%;
        min-height: 161px;
        max-width: 390px
    }

    &__title {
        line-height: 36px;
        font-size: 24px;
        text-transform: uppercase;
        border-bottom: 1px solid #000
    }

    &__subtitle {
        padding: 12px 0 6px
    }
}

.map-accordion-item {
    background: transparent;
    padding: 0;

    button {
        display: flex;
        background: transparent;
        border: none;
        padding: 0;

        &:hover {
            .map-accordion__accordion-arrow {
                filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(334deg) brightness(104%) contrast(101%)
            }
        }
    }

    li {
        a  {
            color: black;
            font-size: 22px;
            font-weight: 700;
            margin-bottom: 12px;
            &:hover{
                color: $pink;
            }
        }

        [data-bs-toggle] {
            color: #000;
            text-decoration: underline;
            margin-bottom: 36px;
        }
    }
}

.collapsed {
    .map-accordion__accordion-arrow {
        display: block;
        width: 7px;
        height: 13px;
        margin: 11px 0 0 15px;
        background: url(assets/img/icon-arrow.svg) center no-repeat;
        transform: rotate(90deg);
        cursor: pointer;
        transition: .3s;

        &:hover {
            filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(334deg) brightness(104%) contrast(101%)
        }
    }
}

.map-accordion__accordion-arrow {
    display: block;
    width: 7px;
    height: 13px;
    margin: 11px 0 0 15px;
    background: url(assets/img/icon-arrow.svg) center no-repeat;
    transform: rotate(270deg);
    cursor: pointer;
    transition: .3s;
}
