.page-404{
	display: flex;
	justify-content: center;
	flex-direction: column;
	.page-title {
		display: flex;

		padding: Min(4vw, 87px) 15px 0;
		position: relative;
		max-width: 2114px;
		width: 100%;
		margin: 0 auto;
		&__item {
			width: 100%;

			.title_1 {
				font-size: Min(5vw, 91px);
				padding: 0 250px 0 50px;

				span {
					margin: 0;
				}
			}
			p {
				padding: 100px 0 0 50px;
				width: 100%;
			}

			&:first-child{
				display: flex;
				align-items: center;
				justify-content: center;
			}

		}

	}
}
