.advantages {
    position: relative;

    &__title {
        position: absolute;
        left: 72px;
        top: -48px;
        font-size: 36px;

        div {
            position: absolute;
            left: 49%;
            top: 30%;
        }
    }

    &__row {

        &.first-child {
            margin-bottom: 88px;
    
            .advantages__row-item {
                position: relative;
                display: flex;
                width: 70%;
                margin-left: auto;
    
                .text-box {
                    position: absolute;
                    left: 22.7%;
                    bottom: 36px;
                    display: grid;
                }
            }
        }
    
        &.last-child {
            display: flex;
            justify-content: space-between;
    
            .advantages__row-item {
                position: relative;
                display: flex;
    
                .text-box {
                    position: absolute;
                    right: 47%;
                    top: 31px;
                }
    
                &:first-child {
                    .text-box {
                        left: 22.7%;
                        top: 74px;
                        bottom: initial;
                    }
                }
    
                &:last-child {
                    i {
                        text-decoration: underline;
                    }
                }
            }
        }
    
        .text-box {
            display: flex;
            color: $black;
            line-height: 1;
            font-size: 5.5vw;
        }
    
        .font-weight-light {
            i {
                font-family: $font-primary;
            }
        }
    }

    &__row-item {
        &:hover {
            .text-box {
                color: $turquoise;
            }
        }
    }

    .fullscreen-bg {
        position: absolute;
        z-index: -1;
        left: 50%;
        top: 50%;
        max-width: 250px;
        transform: translate(-50%, -50%);
    }
}
